import React from 'react'
import { useDispatch } from 'react-redux'
import { TextField } from '@material-ui/core'

import * as routing from '../../../../routing'

import { updateScenario } from '../../../../redux/Scenarios/scenarios.actions'

import LeftDrawer from '../../../../ui/LeftDrawer'

import { useStyles } from '../../../../ui/LeftDrawer/LeftDrawer.style'

const ScenarioEditor = props => {
  const { scenario, isScenarioChanged, setInitialScenario, setScenario } = props

  const dispatch = useDispatch()
  const classes = useStyles()

  const onScenarioTitleChange = e => {
    setScenario({ ...scenario, title: e.target.value })
  }

  const onSave = () => {
    dispatch(updateScenario(scenario))
    setInitialScenario(scenario)
  }

  return (
    <LeftDrawer
      header={{
        title: 'Редактирование сценария',
        backRoute: routing.ROUTE_ADMIN,
        saveButton: {
          disabled: !isScenarioChanged,
          onSave
        }
      }}
    >
      <div className={classes.drawerItem}>
        <div className={classes.drawerItemParam}>
          <div className={classes.drawerItemParam}>
            <TextField
              label="Название сценария"
              variant="outlined"
              fullWidth
              value={scenario.title}
              onChange={onScenarioTitleChange}
            />
          </div>
        </div>
      </div>
    </LeftDrawer>
  )
}

export default ScenarioEditor
