import { useRef } from 'react'

const GAZE_INITIAL_VALUE = { x: 0, y: 0, r: 15, isMoving: false }

const useAutoScroll = () => {
  const gazeRef = useRef(GAZE_INITIAL_VALUE)

  const setGazePosition = e => {
    gazeRef.current.x = e.clientX
    gazeRef.current.y = e.clientY
    gazeRef.current.isMoving = true
  }

  const onGazeMove = e => {
    setGazePosition(e)
  }

  return {
    gaze: gazeRef.current,
    onGazeMove
  }
}

export default useAutoScroll
