import * as api from '../../api'

import * as types from './thesaurus.types'

import { showNotification } from '../Notification/notification.actions'

export const fetchThesaurus = () => (dispatch, getState) => {
  const { thesaurus } = getState()

  if (thesaurus) {
    return
  }

  dispatch({
    type: types.FETCH_THESAURUS,
    payload: api.getThesaurus()
  })
}

const getTermErrors = term => {
  const errors = {}

  if (term.term === '') {
    errors.term = 'Введите термин'
  }

  if (term.definition === '') {
    errors.definition = 'Введите определение термина'
  }

  if (Object.keys(errors).length !== 0) {
    return errors
  }

  return null
}

export const addTerm = term => dispatch => {
  const errors = getTermErrors(term)

  if (errors) {
    return { errors }
  }

  dispatch({
    type: types.ADD_TERM,
    payload: api.addTerm(term)
  })
  dispatch(showNotification('success', `Термин "${term.term}" добавлен`))
}

export const deleteTerm = id => dispatch => {
  dispatch({
    type: types.DELETE_TERM,
    payload: api.deleteTermById(id)
  })
  dispatch(showNotification('warning', `Термин ${id} удален`))
}

export const updateTerm = term => dispatch => {
  const errors = getTermErrors(term)

  if (errors) {
    return { errors }
  }

  dispatch({
    type: types.UPDATE_TERM,
    payload: api.updateTerm(term)
  })
  dispatch(showNotification('success', `Термин "${term.term}" изменен`))
}

export const getTermById = id => (dispatch, getState) => {
  const { thesaurus } = getState()

  return thesaurus.find(term => {
    return term.id === id
  })
}
