import React from 'react'
import { Switch, Route } from 'react-router-dom'

import * as routing from '../../routing'

import AdminSidebar from './components/AdminSidebar'
import LayoutEditorPage from '../../pages/LayoutEditorPage'
import ScenarioEditorPage from '../../pages/ScenarioEditorPage'
import ThesaurusPage from '../../pages/ThesaurusPage'
import MonitoringRecordPage from '../MonitoringRecordPage'

import './AdminPage.css'

const AdminPage = () => {
  return (
    <div className="admin">
      <Switch>
        <Route exact path={routing.ROUTE_ADMIN}>
          <AdminSidebar />
        </Route>
        <Route exact path={routing.ROUTE_LAYOUT_EDITOR}>
          <LayoutEditorPage />
        </Route>
        <Route
          exact
          path={[routing.ROUTE_SCENARIO_EDITOR, routing.ROUTE_BLOCK_EDITOR]}
        >
          <ScenarioEditorPage />
        </Route>
        <Route exact path={routing.ROUTE_THESAURUS}>
          <ThesaurusPage />
        </Route>
        <Route exact path={routing.ROUTE_MONITORING_RECORD}>
          <MonitoringRecordPage />
        </Route>
      </Switch>
    </div>
  )
}

export default AdminPage
