import React from 'react'
import { useHistory } from 'react-router-dom'
import {
  Menu as MuiMenu,
  Divider,
  MenuItem,
  ListItemIcon
} from '@material-ui/core'

import { useStyles } from './Menu.style'

const Menu = props => {
  const { anchorEl, children, onClose } = props

  const history = useHistory()

  const classes = useStyles()

  const isOpened = !!anchorEl

  return (
    <MuiMenu
      anchorEl={anchorEl}
      anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
      keepMounted
      transformOrigin={{ vertical: 'top', horizontal: 'right' }}
      open={isOpened}
      onClose={onClose}
    >
      {children.map((itemGroup, itemGroupIndex) => {
        const renderDivider = () => {
          if (itemGroupIndex === children.length - 1) {
            return null
          }

          return <Divider />
        }

        const items = [
          ...itemGroup.map(item => {
            if (item.visible === false) {
              return null
            }

            const onClick = () => {
              onClose()

              if (item.onClick) {
                item.onClick()
                return
              }

              if (item.to) {
                history.push(item.to)
                return
              }
            }

            const renderIcon = () => {
              if (!item.icon) {
                return null
              }

              return (
                <ListItemIcon className={classes.listItemIcon}>
                  {item.icon}
                </ListItemIcon>
              )
            }

            return (
              <MenuItem key={item.title} dense onClick={onClick}>
                {renderIcon()}
                {item.title}
              </MenuItem>
            )
          }),
          renderDivider()
        ]

        return items
      })}
    </MuiMenu>
  )
}

export default Menu
