import React, { useState, useEffect, useMemo } from 'react'
import { useTheme, Button } from '@material-ui/core'

import { SCENARIO_MODE, BLOCK_TYPE } from '../../constants'

// import { drawGaze, getGazePosition } from '../../services/gaze-service'

import BlocksGroup from '../BlocksGroup'

import { useStyles } from './Content.style'

const Content = props => {
  const {
    modes,
    blocks,
    autoScroll,
    cascade,
    blockActions,
    finishViewing,
    getTextPartClass
  } = props

  const classes = useStyles()
  const theme = useTheme()

  const [contentElement, setContentElement] = useState(null)

  // const addPointToGazeMap = () => {
  //   let { x, y, r } = gazePosition.current

  //   x = Math.round(x)
  //   y -= Math.round(contentRef.current.getBoundingClientRect().top)

  //   const pointsLength = Object.keys(gazeMap.current.points).length
  //   const lastPoint =
  //     gazeMap.current.points[
  //       Object.keys(gazeMap.current.points)[pointsLength - 1]
  //     ]

  //   if (lastPoint) {
  //     const dx = x - lastPoint.x
  //     const dy = y - lastPoint.y
  //     const d = Math.sqrt(dx * dx + dy * dy)

  //     if (d <= r + lastPoint.r) return
  //   }

  //   gazeMap.current.points[new Date() - startDate.current] = {
  //     x,
  //     y,
  //     r
  //   }
  // }

  const blocksGroups = useMemo(() => {
    const groups = []
    let groupIndex = 0

    blocks.forEach((block_, index) => {
      const block = { ...block_, index }

      if (!groups[groupIndex]) {
        groups[groupIndex] = {
          width: 0,
          blocks: []
        }
      }

      const blockWidth =
        block.type === BLOCK_TYPE.TEXT
          ? block.params.textWidth
          : block.params.width
      const currentGroup = groups[groupIndex]

      if (
        (blockWidth > theme.layout.grid.columnsNumber - 2 &&
          currentGroup.width === 0) ||
        currentGroup.width + blockWidth <= theme.layout.grid.columnsNumber
      ) {
        groups[groupIndex].width += blockWidth
        groups[groupIndex].blocks.push(block)
      } else {
        groups.push({
          width: blockWidth,
          blocks: [block]
        })
        groupIndex++
      }
    })

    return groups
  }, [blocks, theme.layout.grid.columnsNumber])

  const execWindowResizeEffect = () => {
    if (!contentElement || !window.onresize) {
      return
    }

    window.onresize()
  }

  useEffect(execWindowResizeEffect, [contentElement])

  return (
    <div ref={setContentElement} className={classes.container}>
      {blocksGroups.map((blocksGroup, blocksGroupIndex) => {
        return (
          <BlocksGroup
            key={blocksGroupIndex}
            blocks={blocksGroup.blocks}
            allBlocksLength={blocks.length}
            modes={modes}
            autoScroll={autoScroll}
            cascade={cascade}
            blockActions={blockActions}
            getTextPartClass={getTextPartClass}
          />
        )
      })}
      {modes.includes(SCENARIO_MODE.VIEW) && (
        <>
          <div className="content-blocks-group">
            <Button
              variant="contained"
              color="primary"
              disableElevation
              onClick={finishViewing}
            >
              Закончить просмотр
            </Button>
          </div>
        </>
      )}
    </div>
  )
}

export default Content
