import React, { useState, useEffect } from 'react'
import { Route, Switch } from 'react-router'
import { useDispatch, useSelector } from 'react-redux'
import { CssBaseline } from '@material-ui/core'
import { ThemeProvider } from '@material-ui/styles'

import { GOLDEN_RATIO_BIG } from '../../constants'

import * as routing from '../../routing'

import { fetchLayout } from '../../redux/Layout/layout.actions'
import { fetchScenarios } from '../../redux/Scenarios/scenarios.actions'
import { fetchThesaurus } from '../../redux/Thesaurus/thesaurus.actions'

import Notification from '../../ui/Notification'
import Header from '../Header'
import AdminPage from '../../pages/AdminPage'
import ScenariosPage from '../../pages/ScenariosPage'

import { theme } from '../../theme'

import './App.css'

export const App = () => {
  const dispatch = useDispatch()

  const layout = useSelector(state => state.layout)
  const scenarios = useSelector(state => state.scenarios)

  const [contentContainerSizes, setContentContainerSizes] = useState({
    width: 0,
    height: 0
  })

  const setOnWindowResizeEffect = () => {
    window.onresize = () => {
      const contentWidthElement = document.querySelector('#contentWidth')
      const contentHeightElement = document.querySelector('#contentHeight')

      if (!contentWidthElement || !contentHeightElement) {
        return
      }

      const { width } = contentWidthElement.getBoundingClientRect()
      const { height } = contentHeightElement.getBoundingClientRect()

      if (
        width === contentContainerSizes.width &&
        height === contentContainerSizes.height
      ) {
        return
      }

      setContentContainerSizes({ width, height })
    }
    window.onresize()
  }

  useEffect(setOnWindowResizeEffect, [contentContainerSizes, layout])

  const resetOnWindowResizeEffect = () => {
    return () => {
      window.onresize = null
    }
  }

  useEffect(resetOnWindowResizeEffect, [])

  const fetchLayoutEffect = () => {
    dispatch(fetchLayout())
  }

  useEffect(fetchLayoutEffect, [dispatch])

  const fetchScenariosEffect = () => {
    dispatch(fetchScenarios())
  }

  useEffect(fetchScenariosEffect, [dispatch])

  const fetchThesaurusEffect = () => {
    dispatch(fetchThesaurus())
  }

  useEffect(fetchThesaurusEffect, [dispatch])

  if (!layout || !scenarios) {
    return null
  }

  const titleFontSize = `${Math.floor(layout.fontSize * GOLDEN_RATIO_BIG)}px`
  const titleLineHeight = `${Math.floor(titleFontSize * GOLDEN_RATIO_BIG)}px`

  const getWidth = columnsNumber => {
    if (!columnsNumber) {
      return 'auto'
    }

    const getGutters = columnsNumber => {
      return layout.grid.columnGutter * (columnsNumber - 1)
    }

    const allGutters = getGutters(layout.grid.columnsNumber)
    const columnWidth =
      (contentContainerSizes.width - allGutters) / layout.grid.columnsNumber
    const gutters = getGutters(columnsNumber)

    return `${columnWidth * columnsNumber + gutters}px`
  }

  const getTop = () => {
    if (contentContainerSizes.height === 0) {
      return '0px'
    }

    return `${Math.round(
      (layout.focusLine * contentContainerSizes.height) / 100
    )}px`
  }

  return (
    <ThemeProvider
      theme={{
        ...theme,
        layout: {
          ...layout,
          titleFontSize,
          titleLineHeight,
          getWidth,
          getTop
        }
      }}
    >
      <CssBaseline />
      <Notification />
      <div className="App">
        <Header />
        <Switch>
          <Route path={routing.ROUTE_ADMIN}>
            <AdminPage />
          </Route>
          <Route path={[routing.ROUTE_ROOT, routing.ROUTE_SCENARIOS]}>
            <ScenariosPage />
          </Route>
        </Switch>
      </div>
    </ThemeProvider>
  )
}
