import React from 'react'
import { useTheme } from '@material-ui/core'

import { SCENARIO_MODE, BLOCK_TYPE } from '../../constants'

import BlockActions from '../../pages/ScenarioEditorPage/components/BlockActions'
import Image from '../Image'
import Text from '../Text'
import TextImageHorizontal from '../TextImageHorizontal'
import TextImageVertical from '../TextImageVertical'
import FadeInContainer from '../../ui/FadeInContainer'

import './Block.css'

const Block = props => {
  const {
    modes,
    block,
    canMoveUp,
    canMoveDown,
    cascade,
    blockActions,
    getTextPartClass
  } = props

  const theme = useTheme()

  const renderBlockActions = () => {
    if (!modes.includes(SCENARIO_MODE.EDIT)) {
      return null
    }

    return (
      <BlockActions
        block={block}
        canMoveUp={canMoveUp}
        canMoveDown={canMoveDown}
        blockActions={blockActions}
      />
    )
  }

  const renderBlock = () => {
    const getWidth = () => {
      if (block.params.width) {
        return theme.layout.getWidth(block.params.width)
      }

      if (block.params.textWidth) {
        return theme.layout.getWidth(block.params.textWidth)
      }

      return 'auto'
    }

    const getHeight = () => {
      if (cascade) {
        return 'auto'
      }

      return '100%'
    }

    const getBlockComponent = props => {
      switch (block.type) {
        case BLOCK_TYPE.TEXT:
          return <Text {...props} />
        case BLOCK_TYPE.IMAGE:
          return <Image {...props} />
        case BLOCK_TYPE.TEXT_IMAGE_HORIZONTAL:
          return <TextImageHorizontal {...props} />
        case BLOCK_TYPE.TEXT_IMAGE_VERTICAL:
          return <TextImageVertical {...props} />
        default:
          return null
      }
    }

    return (
      <div
        className={`content-block content-block-type--${block.type}`}
        style={{ width: getWidth(), height: getHeight() }}
      >
        {getBlockComponent({ block, cascade, getTextPartClass })}
      </div>
    )
  }

  return (
    <FadeInContainer modes={modes}>
      {renderBlockActions()}
      {renderBlock()}
    </FadeInContainer>
  )
}

export default Block
