import React, { useState, useEffect } from 'react'
import { useParams, Redirect } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'

import { SCENARIO_MODE } from '../../constants'

import * as routing from '../../routing'

import * as contentApi from '../../api/content'

import { getScenarioById } from '../../redux/Scenarios/scenarios.actions'
import { getTermById } from '../../redux/Thesaurus/thesaurus.actions'

import ContentContainer from '../ContentContainer'
import AddBlockDialog from '../../pages/ScenarioEditorPage/components/AddBlockDialog'

const Scenario = props => {
  const {
    scenario,
    modes,
    autoScroll,
    cascade,
    containerWidth,
    blockActions,
    setScenarioBlocks,
    getTextPartClass
  } = props

  const dispatch = useDispatch()

  const thesaurus = useSelector(state => state.thesaurus)

  const { scenarioId } = useParams()

  const [blocks, setBlocks] = useState(null)

  const setBlocksEffect = () => {
    if (scenario || blocks) {
      return
    }

    const _scenario = dispatch(getScenarioById(scenarioId))

    if (!_scenario) {
      setBlocks(undefined)
      return
    }

    const result = contentApi.splitContentBlocksIntoWords(
      Object.fromEntries(
        _scenario.blocks
          .filter(block => {
            return block.text
          })
          .map(block => {
            return [block.id, block.text]
          })
      ),
      thesaurus,
      !modes.includes(SCENARIO_MODE.EXAMPLE)
    )
    const splittedBlocks = _scenario.blocks.map(block => {
      const blockText = result[block.id]

      if (!blockText) {
        return block
      }

      return { ...block, text: blockText }
    })

    setBlocks(splittedBlocks)
  }

  useEffect(setBlocksEffect, [
    blocks,
    dispatch,
    modes,
    scenario,
    scenarioId,
    thesaurus
  ])

  const _blocks = scenario?.blocks || blocks

  if (!_blocks) {
    if (_blocks === undefined) {
      return <Redirect to={routing.ROUTE_SCENARIOS} />
    }

    return null
  }

  const expandTerms = terms => {
    setBlocks(
      _blocks.map(block => {
        if (!block.text) {
          return block
        }

        const getText = () => {
          //! MUTATION

          const text = JSON.parse(JSON.stringify(block.text))

          for (let i = 0; i < terms.length; i++) {
            const [blockId, paragraphIndex, termId] = terms[i].split('__')

            if (block.id !== blockId) {
              continue
            }

            const term = dispatch(getTermById(termId))
            const splittedTerm = {
              ...term,
              definition: contentApi.splitTextIntoParts(
                term.definition,
                thesaurus,
                true
              )
            }

            text.splice(parseInt(paragraphIndex) + 1, 0, splittedTerm)
          }

          return text
        }

        return {
          ...block,
          text: getText()
        }
      })
    )
  }

  return (
    <>
      <ContentContainer
        autoScroll={autoScroll}
        blocks={_blocks}
        modes={modes}
        cascade={cascade}
        containerWidth={containerWidth}
        blockActions={blockActions}
        expandTerms={expandTerms}
        getTextPartClass={getTextPartClass}
      />
      {modes.includes(SCENARIO_MODE.EDIT) && (
        <AddBlockDialog
          scenario={scenario}
          setScenarioBlocks={setScenarioBlocks}
        />
      )}
    </>
  )
}

export default Scenario
