import React, { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import {
  List,
  ListItem,
  ListItemText,
  Divider,
  Collapse,
  IconButton,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  Button,
  Tooltip
} from '@material-ui/core'
import { Alert } from '@material-ui/lab'
import { Add, Remove, ExpandMore, ExpandLess } from '@material-ui/icons'
import { format } from 'date-fns'
import { ru } from 'date-fns/locale'

import * as api from '../../../../api'

import * as routing from '../../../../routing'

import { showNotification } from '../../../../redux/Notification/notification.actions'

import {
  addScenario,
  deleteScenario
} from '../../../../redux/Scenarios/scenarios.actions'

import LeftDrawer from '../../../../ui/LeftDrawer'
import AddDialog from '../../../../ui/AddDialog'

import { useStyles } from '../../../../ui/LeftDrawer/LeftDrawer.style'

const ADD_SCENARIO_DIALOG_INITIAL_STATE = {
  isOpened: false,
  title: '',
  errors: {}
}
const STORAGE_DIALOG_INITIAL_STATE = {
  isOpened: false,
  storage: ''
}

const AdminSidebar = () => {
  const dispatch = useDispatch()
  const classes = useStyles()
  const scenarios = useSelector(state => state.scenarios)

  const [addScenarioDialogState, setAddScenarioDialogState] = useState(
    ADD_SCENARIO_DIALOG_INITIAL_STATE
  )
  const [storageDialogState, setStorageDialogState] = useState(
    STORAGE_DIALOG_INITIAL_STATE
  )
  const [isMonitoringExpanded, setIsMonitoringExpanded] = useState(false)
  const [monitoringRecords, setMonitoringRecords] = useState(null)

  const fetchMonitoringRecordsEffect = () => {
    if (monitoringRecords) {
      return
    }

    setMonitoringRecords(api.getMonitoringRecords())
  }

  useEffect(fetchMonitoringRecordsEffect, [monitoringRecords])

  const renderScenarios = () => {
    const onAddScenarioDialogOpen = () => {
      setAddScenarioDialogState({ ...addScenarioDialogState, isOpened: true })
    }

    const onAddScenarioDialogClose = () => {
      setAddScenarioDialogState(ADD_SCENARIO_DIALOG_INITIAL_STATE)
    }

    const onScenarioTitleChange = e => {
      setAddScenarioDialogState({
        ...addScenarioDialogState,
        title: e.target.value,
        errors: {}
      })
    }

    const onAddScenario = () => {
      const result = dispatch(addScenario(addScenarioDialogState.title))

      if (result?.errors) {
        setAddScenarioDialogState({
          ...addScenarioDialogState,
          errors: result.errors
        })
        return false
      }

      return true
    }

    const renderList = () => {
      if (!scenarios) {
        return null
      }

      return (
        <Collapse in={true} timeout="auto" unmountOnExit>
          <List component="div" disablePadding>
            {scenarios.map(scenario => {
              const onDeleteScenario = e => {
                e.preventDefault()
                dispatch(deleteScenario(scenario.id))
              }

              return (
                <ListItem
                  key={scenario.id}
                  className={classes.nestedListItem}
                  button
                  component={Link}
                  to={routing.getRouteToScenarioEditor(scenario.id)}
                >
                  <ListItemText primary={scenario.title} />
                  <Tooltip title="Удалить сценарий" placement="right">
                    <IconButton
                      size="small"
                      color="secondary"
                      onClick={onDeleteScenario}
                    >
                      <Remove />
                    </IconButton>
                  </Tooltip>
                </ListItem>
              )
            })}
          </List>
        </Collapse>
      )
    }

    return (
      <>
        <AddDialog
          isOpened={addScenarioDialogState.isOpened}
          title="Добавление сценария"
          onAdd={onAddScenario}
          onClose={onAddScenarioDialogClose}
        >
          <TextField
            error={!!addScenarioDialogState.errors.title}
            helperText={addScenarioDialogState.errors.title}
            variant="outlined"
            label="Название сценария"
            value={addScenarioDialogState.title}
            autoFocus
            fullWidth
            margin="normal"
            required
            onChange={onScenarioTitleChange}
          />
        </AddDialog>
        <ListItem className={classes.listItem}>
          <ListItemText primary="Сценарии" />
          <Tooltip title="Добавить сценарий" placement="right">
            <IconButton
              size="small"
              color="primary"
              onClick={onAddScenarioDialogOpen}
            >
              <Add />
            </IconButton>
          </Tooltip>
        </ListItem>
        {renderList()}
        <Divider />
      </>
    )
  }

  const renderStorage = () => {
    const onStorageDialogOpen = () => {
      setStorageDialogState({ isOpened: true, storage: api.exportStorage() })
    }

    const onStorageDialogClose = () => {
      setStorageDialogState(STORAGE_DIALOG_INITIAL_STATE)
    }

    const onStorageChange = e => {
      setStorageDialogState({ ...storageDialogState, storage: e.target.value })
    }

    const onImportStorage = () => {
      onStorageDialogClose()
      api.importStorage(storageDialogState.storage)
    }

    return (
      <>
        <Dialog
          open={storageDialogState.isOpened}
          fullWidth
          onClose={onStorageDialogClose}
        >
          <DialogTitle>Импорт / Экспорт хранилища</DialogTitle>
          <DialogContent>
            <Alert severity="error">
              После импорта хранилища перезагрузите страницу.
            </Alert>
            <TextField
              variant="outlined"
              label="Хранилище"
              value={storageDialogState.storage}
              autoFocus
              fullWidth
              margin="normal"
              required
              onChange={onStorageChange}
            />
          </DialogContent>
          <DialogActions>
            <Button color="primary" onClick={onImportStorage}>
              Импортировать
            </Button>
            <Button color="default" onClick={onStorageDialogClose}>
              Отмена
            </Button>
          </DialogActions>
        </Dialog>
        <ListItem
          className={classes.listItem}
          button
          onClick={onStorageDialogOpen}
        >
          <ListItemText primary="Хранилище" secondary="Импорт / Экспорт" />
        </ListItem>
        <Divider />
      </>
    )
  }

  const renderMonitoring = () => {
    const onToggleMonitoring = () => {
      setIsMonitoringExpanded(!isMonitoringExpanded)
    }

    const renderExpandButton = () => {
      if (!monitoringRecords?.length) {
        return null
      }

      return (
        <IconButton size="small" color="primary" onClick={onToggleMonitoring}>
          {isMonitoringExpanded ? <ExpandLess /> : <ExpandMore />}
        </IconButton>
      )
    }

    const renderList = () => {
      if (!monitoringRecords?.length) {
        return null
      }

      return (
        <Collapse in={isMonitoringExpanded} timeout="auto" unmountOnExit>
          <List component="div" disablePadding>
            {monitoringRecords.map(record => {
              const onDeleteMonitoringRecord = e => {
                e.preventDefault()
                setMonitoringRecords(api.deleteMonitoringRecordById(record.id))
                dispatch(
                  showNotification(
                    'warning',
                    `Запись о мониторинге ${record.id} удалена`
                  )
                )
              }

              const renderTitle = () => {
                return format(new Date(record.date), 'dd MMMM yyyy HH:mm', {
                  locale: ru
                })
              }

              return (
                <ListItem
                  key={record.id}
                  className={classes.nestedListItem}
                  button
                  component={Link}
                  to={routing.getRouteToMonitoringRecord(record.id)}
                >
                  <ListItemText primary={renderTitle()} />
                  <Tooltip title="Удалить запись" placement="right">
                    <IconButton
                      size="small"
                      color="secondary"
                      onClick={onDeleteMonitoringRecord}
                    >
                      <Remove />
                    </IconButton>
                  </Tooltip>
                </ListItem>
              )
            })}
          </List>
        </Collapse>
      )
    }

    return (
      <>
        <ListItem
          className={classes.listItem}
          disabled={!monitoringRecords?.length}
        >
          <ListItemText primary="Мониторинг" />
          {renderExpandButton()}
        </ListItem>
        {renderList()}
        <Divider />
      </>
    )
  }

  return (
    <LeftDrawer>
      <List disablePadding>
        <ListItem
          className={classes.listItem}
          button
          component={Link}
          to={routing.ROUTE_LAYOUT_EDITOR}
        >
          <ListItemText
            primary="Параметры представления"
            secondary="Общие настройки представления контента"
          />
        </ListItem>
        <Divider />
        <ListItem
          className={classes.listItem}
          button
          component={Link}
          to={routing.ROUTE_THESAURUS}
        >
          <ListItemText
            primary="Тезаурус"
            secondary="Словарь предметной области"
          />
        </ListItem>
        <Divider />
        {renderStorage()}
        {renderScenarios()}
        {renderMonitoring()}
        {/* <ListItem
          className={classes.listItem}
          button
          component={Link}
          to={routing.ROUTE_USERS}
        >
          <ListItemText primary="Пользователи" />
        </ListItem>
        <Divider /> */}
      </List>
    </LeftDrawer>
  )
}

export default AdminSidebar
