import React, { useState, useEffect } from 'react'
import { useInView } from 'react-intersection-observer'

import { SCENARIO_MODE } from '../../constants'

import { joinClasses } from '../../helpers'

import './FadeInContainer.css'

const FadeInBlock = props => {
  const { modes, children } = props

  const { ref, inView } = useInView()

  const [isVisible, setIsVisible] = useState(false)

  const setIsVisibleEffect = () => {
    if (isVisible || !inView) {
      return
    }

    setIsVisible(true)
  }

  useEffect(setIsVisibleEffect, [inView, isVisible])

  if (!modes.includes(SCENARIO_MODE.VIEW)) {
    return children
  }

  return (
    <div
      ref={ref}
      className={joinClasses('fade-in-container', isVisible && 'is-visible')}
    >
      {children}
    </div>
  )
}

export default FadeInBlock
