import React from 'react'
import { useParams } from 'react-router-dom'
import { useTheme } from '@material-ui/core'

import { BLOCK_TYPE } from '../../constants'

import { joinClasses } from '../../helpers'

import Block from '../Block'

import { useStyles } from './BlocksGroup.style'

const BlocksGroup = props => {
  const {
    blocks,
    allBlocksLength,
    modes,
    autoScroll,
    cascade,
    blockActions,
    getTextPartClass
  } = props

  const theme = useTheme()

  const classes = useStyles()

  const { blockId } = useParams()

  return (
    <div className={classes.container}>
      {blocks.map((block, blockIndex) => {
        const style = {
          width: theme.layout.getWidth(
            block.params.width || block.params.textWidth
          )
        }

        if (blocks.length > 1 && blockIndex > 0) {
          style.marginLeft = theme.layout.grid.columnGutter
        }

        const isHighlightedBlock = blockId && block.id === blockId
        const isNotHighlightedBlock = blockId && block.id !== blockId

        const setCascadePosition = el => {
          //! ПРОТЕСТИРОВАТЬ НА ВСЕХ ВИДАХ БЛОКОВ

          if (!cascade || !el?.previousSibling) {
            return
          }

          const getSiblingHeight = () => {
            const getSiblingBounds = () => {
              if (autoScroll) {
                return el.previousSibling.childNodes[0].childNodes[0].getBoundingClientRect()
              }

              return el.previousSibling.childNodes[0].getBoundingClientRect()
            }

            const siblingBounds = getSiblingBounds()

            return siblingBounds.height
          }

          const getSiblingMarginTop = () => {
            return parseFloat(el.previousSibling.style.marginTop) || 0
          }

          const getImageHeight = () => {
            if (block.type !== BLOCK_TYPE.TEXT_IMAGE_VERTICAL) {
              return 0
            }

            const imageEl = el.querySelector('.image')
            const imageBounds = imageEl.getBoundingClientRect()

            return imageBounds.height
          }

          const marginTop =
            getSiblingHeight() +
            getSiblingMarginTop() -
            theme.layout.lineHeight * 3 -
            getImageHeight()

          el.style.marginTop = `${marginTop}px`
        }

        return (
          <div
            key={blockIndex}
            ref={setCascadePosition}
            className={joinClasses(
              'content-block-container',
              isHighlightedBlock && 'highlighted-block',
              isNotHighlightedBlock && 'not-highlighted-block'
            )}
            style={style}
          >
            <Block
              block={block}
              canMoveUp={block.index !== 0}
              canMoveDown={block.index !== allBlocksLength - 1}
              modes={modes}
              cascade={cascade}
              blockActions={blockActions}
              getTextPartClass={getTextPartClass}
            />
          </div>
        )
      })}
    </div>
  )
}

export default BlocksGroup
