import React from 'react'
import { useTheme } from '@material-ui/core'

import { BLOCK_TYPE } from '../../constants'

import './Image.css'

const Image = props => {
  const { block } = props

  const theme = useTheme()

  return (
    <div
      className="content-block-item image"
      style={{
        backgroundImage: `url(${block.image.src})`,
        backgroundSize: block.image.size,
        height: block.image.height
          ? `${theme.layout.lineHeight * block.image.height}px`
          : 'auto',
        top:
          block.type === BLOCK_TYPE.TEXT_IMAGE_HORIZONTAL
            ? theme.layout.getTop()
            : 'auto'
      }}
    ></div>
  )
}

export default Image
