const SCENARIO_ID = ':scenarioId'
const BLOCK_ID = ':blockId'
const MONITORING_RECORD_ID = ':monitoringRecordId'

export const ROUTE_ROOT = '/'

export const ROUTE_SCENARIOS = `${ROUTE_ROOT}scenarios`
export const ROUTE_SCENARIO = `${ROUTE_SCENARIOS}/${SCENARIO_ID}`

export const getRouteToScenario = scenarioId => {
  return ROUTE_SCENARIO.replace(SCENARIO_ID, scenarioId)
}

export const ROUTE_ADMIN = `${ROUTE_ROOT}admin`

export const ROUTE_LAYOUT_EDITOR = `${ROUTE_ADMIN}/layout-editor`

export const ROUTE_SCENARIO_EDITOR = `${ROUTE_ADMIN}/scenarios/:scenarioId`

export const getRouteToScenarioEditor = scenarioId => {
  return ROUTE_SCENARIO_EDITOR.replace(SCENARIO_ID, scenarioId)
}

export const ROUTE_BLOCK_EDITOR = `${ROUTE_SCENARIO_EDITOR}/block-editor/${BLOCK_ID}`

export const getRouteToBlockEditor = (scenarioId, blockId) => {
  return ROUTE_BLOCK_EDITOR.replace(SCENARIO_ID, scenarioId).replace(
    BLOCK_ID,
    blockId
  )
}

export const ROUTE_THESAURUS = `${ROUTE_ADMIN}/thesaurus`

export const ROUTE_MONITORING_RECORD = `${ROUTE_ADMIN}/monitoring/${MONITORING_RECORD_ID}`

export const getRouteToMonitoringRecord = id => {
  return ROUTE_MONITORING_RECORD.replace(MONITORING_RECORD_ID, id)
}

