import { circleRectCollision } from '../helpers'

import { TEXT_PART_TYPE } from './content'

const MAX_RECOGNITION_FIX_DURATION = 750

const INITIAL_STATE = {
  parts: {},
  prevHoveredParts: []
}

let STATE = null

const initState = () => {
  STATE = JSON.parse(JSON.stringify(INITIAL_STATE))
}

initState()

//? Нужно добавить условие для повторного раскрытия понятий
//! Проблема с partId: когда раскрываются понятия, в partId слов абзаца, который расположен ниже блока с определением, меняется часть, отвечающая за индекс абзаца, из-за этого новый partId больше не связан с записанными параметрами слова
//! Проблема с раскрытием блоков (глобально - с изменением содержания, размеров контента): некоторые координаты взгляда становятся неактуальными, например, изменилась высота блока, и теперь взгляд "смотрит" не туда, куда смотрел до изменения высоты

export const getTermsToExpand = (gazeCircle, contentMarkup) => {
  const getHoveredParts = partType => {
    const getMarkup = () => {
      switch (partType) {
        case TEXT_PART_TYPE.TERM:
          return contentMarkup.terms
        case TEXT_PART_TYPE.WORD:
          return contentMarkup.words
        default:
          return null
      }
    }

    return Object.entries(getMarkup())
      .filter(([partId, rect]) => {
        return circleRectCollision(gazeCircle, rect)
      })
      .map(([partId]) => {
        return {
          type: partType,
          id: partId.split('__')[2],
          partId
        }
      })
  }

  const hoveredTerms = getHoveredParts(TEXT_PART_TYPE.TERM)
  const hoveredWords = getHoveredParts(TEXT_PART_TYPE.WORD)
  const hoveredParts = [...hoveredTerms, ...hoveredWords]

  const termsToExpand = []

  hoveredParts.forEach(({ type, id, partId }) => {
    if (!STATE.parts[partId]) {
      STATE.parts[partId] = {
        id,
        type,
        isRecognized: null,
        fixations: []
      }
    }

    const prevHoveredPartIndex = STATE.prevHoveredParts.findIndex(
      prevHoveredPart => {
        return prevHoveredPart.partId === partId
      }
    )

    if (prevHoveredPartIndex === -1) {
      STATE.parts[partId].fixations.push([Date.now()])
    } else {
      STATE.prevHoveredParts.splice(prevHoveredPartIndex, 1)
    }

    if (STATE.parts[partId].isRecognized === false) {
      return
    }

    const fixDuration = STATE.parts[partId].fixations.reduce(
      (duration, fixation) => {
        const [begin] = fixation
        const end = fixation[1] || Date.now()

        return duration + (end - begin)
      },
      0
    )
    const isRecognized = fixDuration <= MAX_RECOGNITION_FIX_DURATION

    if (type === TEXT_PART_TYPE.TERM) {
      const isTermAlreadyExpanded = () => {
        return !!Object.values(STATE.parts).find(part => {
          return part.id === id && !part.isRecognized
        })
      }

      if (!isRecognized && !isTermAlreadyExpanded()) {
        termsToExpand.push(partId)
      }
    }

    STATE.parts[partId].isRecognized = isRecognized
  })

  STATE.prevHoveredParts.forEach(({ partId }) => {
    const { fixations } = STATE.parts[partId]

    STATE.parts[partId].fixations[fixations.length - 1].push(Date.now())
  })

  STATE.prevHoveredParts = hoveredParts

  return termsToExpand
}

export const getMonitoringData = () => {
  return STATE.parts
}

export const clearState = () => {
  initState()
}
