import React from 'react'
import { Route, Switch, Link } from 'react-router-dom'
import { useSelector } from 'react-redux'
import { Grid, Button } from '@material-ui/core'

import { SCENARIO_MODE } from '../../constants'

import * as routing from '../../routing'

import Scenario from '../../components/Scenario'

import { useStyles } from './ScenariosPage.style'

const ScenariosPage = () => {
  const scenarios = useSelector(state => state.scenarios)

  const classes = useStyles()

  return (
    <Switch>
      <Route exact path={routing.ROUTE_SCENARIO}>
        <Scenario modes={[SCENARIO_MODE.VIEW]} autoScroll cascade />
      </Route>
      <Route>
        <Grid
          className={classes.root}
          container
          justify="center"
          alignItems="center"
        >
          <Grid
            item
            container
            xs={12}
            spacing={2}
            justify="center"
            style={{ maxWidth: 1280 }}
          >
            {scenarios.map(scenario => (
              <Grid key={scenario.id} item xs={4}>
                <Button
                  className={classes.scenario}
                  variant="outlined"
                  component={Link}
                  to={routing.getRouteToScenario(scenario.id)}
                  fullWidth
                >
                  {scenario.title}
                </Button>
              </Grid>
            ))}
          </Grid>
        </Grid>
      </Route>
    </Switch>
  )
}

export default ScenariosPage
