import React, { useState } from 'react'
import { useDispatch } from 'react-redux'
import {
  useTheme,
  Card,
  CardContent,
  IconButton,
  CardHeader,
  Avatar
} from '@material-ui/core'
import { Edit, Delete, School, MoreHoriz } from '@material-ui/icons'

import { deleteTerm } from '../../redux/Thesaurus/thesaurus.actions'

import Menu from '../Menu'
import SplittedText from '../SplittedText'

import { useStyles } from './TermCard.style'

const TermCard = props => {
  const { term, showMenu, splittedTextData, onTermDialogOpen } = props

  const dispatch = useDispatch()

  const classes = useStyles()

  const theme = useTheme()

  const [anchorEl, setAnchorEl] = useState(null)

  const onMenuOpen = e => {
    setAnchorEl(e.currentTarget)
  }

  const onMenuClose = () => {
    setAnchorEl(null)
  }

  const title = term.term.toLowerCase()
  const synonyms = term.synonyms.map(synonym => {
    return synonym.toLowerCase()
  })

  const menuItems = [
    [
      {
        title: 'Редактировать',
        icon: <Edit color="primary" />,
        onClick: () => {
          onTermDialogOpen(term)
        }
      },
      {
        title: 'Удалить',
        icon: <Delete color="secondary" />,
        onClick: () => {
          dispatch(deleteTerm(term.id))
        }
      }
    ]
  ]

  const renderSubheader = () => {
    return synonyms.map((synonym, i) => {
      const renderBull = () => {
        if (i !== synonyms.length - 1) {
          return <>&nbsp;&bull;&nbsp;</>
        }

        return null
      }

      return (
        <React.Fragment key={i}>
          {synonym}
          {renderBull()}
        </React.Fragment>
      )
    })
  }

  const renderDefinition = () => {
    if (!splittedTextData) {
      return term.definition
    }

    const { blockId, paragraphIndex } = splittedTextData

    return (
      <SplittedText blockId={blockId} paragraphIndex={paragraphIndex}>
        {term.definition}
      </SplittedText>
    )
  }

  return (
    <>
      <Menu anchorEl={anchorEl} onClose={onMenuClose}>
        {menuItems}
      </Menu>
      <Card className={classes.card} variant="outlined" elevation={3}>
        <CardHeader
          className={classes.header}
          title={title}
          subheader={renderSubheader()}
          avatar={
            <Avatar
              style={{
                // backgroundColor: theme.palette.primary.light,
                backgroundColor: 'rgb(232, 244, 253)',
                width: theme.spacing(5),
                height: theme.spacing(5)
              }}
            >
              <School
                style={{
                  // color: '#fff',
                  color: '#2196f3',
                  width: theme.spacing(3),
                  height: theme.spacing(3)
                }}
              />
            </Avatar>
          }
          action={
            showMenu && (
              <IconButton
                size="small"
                style={{ marginLeft: theme.spacing(2) }}
                onClick={onMenuOpen}
              >
                <MoreHoriz />
              </IconButton>
            )
          }
        />
        <CardContent className={classes.content}>
          <p>{renderDefinition()}</p>
        </CardContent>
      </Card>
    </>
  )
}

export default TermCard
