import * as api from '../../api'

import * as types from './scenarios.types'

import { showNotification } from '../Notification/notification.actions'

export const fetchScenarios = () => (dispatch, getState) => {
  const { scenarios } = getState()

  if (scenarios) {
    return
  }

  dispatch({
    type: types.FETCH_SCENARIOS,
    payload: api.getScenarios()
  })
}

export const addScenario = title => dispatch => {
  if (title === '') {
    return {
      errors: {
        title: 'Введите название'
      }
    }
  }

  dispatch({
    type: types.ADD_SCENARIO,
    payload: api.addScenario(title)
  })
  dispatch(showNotification('success', `Сценарий "${title}" создан`))
}

export const deleteScenario = id => dispatch => {
  dispatch({
    type: types.DELETE_SCENARIO,
    payload: api.deleteScenarioById(id)
  })
  dispatch(showNotification('warning', `Сценарий ${id} удален`))
}

export const updateScenario = scenario => dispatch => {
  dispatch({
    type: types.UPDATE_SCENARIO,
    payload: api.updateScenario(scenario)
  })
  dispatch(showNotification('success', `Сценарий "${scenario.title}" сохранен`))
}

export const getScenarioById = id => (dispatch, getState) => {
  const { scenarios } = getState()

  const scenario = scenarios.find(scenario => {
    return scenario.id === id
  })

  if (!scenario) {
    return undefined
  }

  return {
    ...scenario,
    blocks: api.getScenarioBlocks(scenario)
  }
}
