import React, { useState } from 'react'
import { useHistory, useParams } from 'react-router-dom'
import { useDispatch } from 'react-redux'
import { IconButton } from '@material-ui/core'
import {
  MoreHoriz,
  ArrowUpward,
  ArrowDownward,
  VerticalAlignTop,
  VerticalAlignBottom,
  Edit,
  Delete
} from '@material-ui/icons'

import * as routing from '../../../../routing'

import { toggleAddBlockDialog } from '../../../../redux/BlockEditor/blockEditor.actions'

import Menu from '../../../../ui/Menu'

import { useStyles } from './BlockActions.style'

import './BlockActions.css'

const BlockActions = props => {
  const { block, canMoveUp, canMoveDown, blockActions } = props

  const dispatch = useDispatch()
  const history = useHistory()
  const { scenarioId, blockId } = useParams()

  const classes = useStyles()

  const [anchorEl, setAnchorEl] = useState(null)

  const handleMenuOpen = e => {
    setAnchorEl(e.currentTarget)
  }

  const handleMenuClose = () => {
    setAnchorEl(null)
  }

  const moveBlockUp = () => {
    handleMenuClose()
    blockActions.moveUp(block)
  }

  const moveBlockDown = () => {
    handleMenuClose()
    blockActions.moveDown(block)
  }

  const removeBlock = () => {
    handleMenuClose()
    blockActions.delete(block)

    if (blockId) {
      history.push(routing.getRouteToScenarioEditor(scenarioId))
    }
  }

  const addBlock = direction => {
    handleMenuClose()
    dispatch(toggleAddBlockDialog(block.order, direction))
  }

  const menuItems = [
    [
      {
        title: 'Редактировать',
        icon: <Edit color="primary" />,
        to: routing.getRouteToBlockEditor(scenarioId, block.id)
      },
      {
        visible: canMoveUp,
        title: 'Переместить вверх',
        icon: <ArrowUpward color="primary" />,
        onClick: moveBlockUp
      },
      {
        visible: canMoveDown,
        title: 'Переместить вниз',
        icon: <ArrowDownward color="primary" />,
        onClick: moveBlockDown
      },
      {
        title: 'Удалить',
        icon: <Delete color="secondary" />,
        onClick: removeBlock
      }
    ],
    [
      {
        title: 'Добавить блок сверху',
        icon: <VerticalAlignTop color="primary" />,
        onClick: () => addBlock(-1)
      },
      {
        title: 'Добавить блок снизу',
        icon: <VerticalAlignBottom color="primary" />,
        onClick: () => addBlock(1)
      }
    ]
  ]

  return (
    <>
      <div className="content-block-actions-container">
        <IconButton
          className={classes.icon}
          size="small"
          onClick={handleMenuOpen}
        >
          <MoreHoriz />
        </IconButton>
      </div>
      <Menu anchorEl={anchorEl} onClose={handleMenuClose}>
        {menuItems}
      </Menu>
    </>
  )
}

export default BlockActions
