import axios from 'axios'
import { nanoid } from 'nanoid'

import { GOLDEN_RATIO } from '../constants'

const YANDEX_DICTIONARY_API_URL = 'https://dictionary.yandex.net/api/v1'
const YANDEX_DICTIONARY_API_KEY =
  'dict.1.1.20220428T193402Z.bbc72334b78ae239.c390768c1f31e22a9869ba21e343dc717395e419'
const LANG = 'ru-ru'
const WORDS_EQUALITY_THRESHOLD = 0.7

export const TEXT_PART_TYPE = {
  WORD: 'WORD',
  PUNCTUATION: 'PUNCTUATION',
  TERM: 'TERM'
}

const _getQueryParamsString = params => {
  const searchParams = new URLSearchParams()

  Object.entries(params).forEach(([key, value]) => {
    searchParams.append(key, value)
  })

  return searchParams.toString()
}

export const getTermSynonyms = async term => {
  if (!term) {
    return []
  }

  try {
    const query = _getQueryParamsString({
      key: YANDEX_DICTIONARY_API_KEY,
      lang: LANG,
      text: term
    })
    const response = await axios.get(
      `${YANDEX_DICTIONARY_API_URL}/dicservice.json/lookup?${query}`,
      {
        withCredentials: false
      }
    )
    const synonyms = [
      ...new Set(
        response.data.def.reduce((defs, def) => {
          return [
            ...defs,
            ...(def.tr || []).reduce((trs, tr) => {
              return [
                ...trs,
                tr.text,
                ...(tr.syn || []).reduce((synonyms, syn) => {
                  return [...synonyms, syn.text]
                }, [])
              ]
            }, [])
          ]
        }, [])
      )
    ]

    return synonyms
  } catch (err) {
    console.error(err)

    return []
  }
}

const _areWordsEqual = (w1, w2) => {
  const w1Parts = w1.split(' ')
  const w2Parts = w2.split(' ')

  if (w1Parts.length !== w2Parts.length) {
    return false
  }

  const partsNumber = w1Parts.length
  const result = [...new Array(partsNumber)].map((_, j) => {
    const words = [w1Parts[j], w2Parts[j]]
      .map(w => {
        return w.toLowerCase()
      })
      .sort((w1, w2) => {
        return w1.length - w2.length
      })

    let charsCounter = 0

    for (let i = 0; i < words[0].length; i++) {
      if (words[0][i] === words[1][i]) {
        charsCounter++
      }
    }

    return charsCounter / words[1].length >= WORDS_EQUALITY_THRESHOLD
  })

  return result.filter(Boolean).length === partsNumber
}

export const splitTextIntoParts = (text, thesaurus, weigh) => {
  if (typeof text !== 'string') {
    return text
  }

  const excluded = '-'
  const words = [...text.matchAll(/[а-яё-]+/gi)].filter(word => {
    return !excluded.includes(word[0])
  })
  const parts = []

  const pushWord = value => {
    parts.push({
      id: nanoid(),
      type: TEXT_PART_TYPE.WORD,
      value
    })
  }

  const pushPunctuation = value => {
    parts.push({
      type: TEXT_PART_TYPE.PUNCTUATION,
      value
    })
  }

  for (let i = 0; i < words.length; i++) {
    const { index } = words[i]
    const [word] = words[i]
    const wordLength = word.length

    if (i === 0 && index !== 0) {
      pushPunctuation(text.slice(0, index))
    }

    pushWord(word)

    const limit = words[i + 1]?.index

    pushPunctuation(text.slice(index + wordLength, limit))
  }

  const sortedThesaurus = thesaurus
    .reduce((terms, term) => {
      return [
        ...terms,
        {
          id: term.id,
          term: term.term
        },
        ...term.synonyms.map(synonym => {
          return {
            id: term.id,
            term: synonym
          }
        })
      ]
    }, [])
    .sort((a, b) => {
      return b.term.split(' ').length - a.term.split(' ').length
    })

  sortedThesaurus.forEach(term => {
    const getTermFromThesaurus = () => {
      return thesaurus.find(_term => {
        return _term.id === term.id
      })
    }

    const _parts = term.term.split(' ')
    const num = _parts.length + _parts.length - 1

    const checkCondition = part => {
      return [...new Array(num)]
        .map((_, i) => {
          if (i % 2 === 0) {
            return TEXT_PART_TYPE.WORD
          }

          return ' '
        })
        .every((item, i) => {
          if (item === ' ') {
            return part[i].value === item
          }

          return part[i].type === item
        })
    }

    for (let i = 0; i < parts.length - num + 1; i++) {
      const part = parts.slice(i, i + num)

      if (!checkCondition(part)) {
        continue
      }

      const partJoined = part
        .map(item => {
          return item.value
        })
        .join('')

      if (_areWordsEqual(term.term, partJoined)) {
        parts.splice(i, num, {
          id: term.id,
          type: TEXT_PART_TYPE.TERM,
          value: partJoined,
          term: getTermFromThesaurus()
        })
      }
    }
  })

  const maxWordLength = weigh
    ? Math.max(
        ...parts.map(part => {
          if (part.type !== TEXT_PART_TYPE.WORD) {
            return 0
          }

          return part.value.length
        })
      )
    : 1

  //? Оставляем только "семантически значимые" слова
  //? Вообще, это должно быть более сложным алгоритмом,
  //? с учетом морфологии, ИПС и т.д.
  const weightedParts = parts.map(part => {
    if (
      part.type !== TEXT_PART_TYPE.WORD ||
      part.value.length / maxWordLength >= GOLDEN_RATIO
    ) {
      return part
    }

    return {
      type: TEXT_PART_TYPE.PUNCTUATION,
      value: part.value
    }
  })

  return weightedParts
}

export const splitContentBlocksIntoWords = (
  blocks,
  thesaurus,
  weigh = true
) => {
  return Object.fromEntries(
    Object.entries(blocks).map(([blockId, paragraphs]) => {
      return [
        blockId,
        paragraphs.map(p => {
          return splitTextIntoParts(p, thesaurus, weigh)
        })
      ]
    })
  )
}
