import { nanoid } from 'nanoid'

const STORAGE = window.localStorage

export const STORAGE_ITEM = {
  LAYOUT: 'LAYOUT',
  BLOCKS: 'BLOCKS',
  SCENARIOS: 'SCENARIOS',
  THESAURUS: 'THESAURUS',
  MONITORING: 'MONITORING'
}
export const STORAGE_ITEM_INITIAL_VALUE = {
  [STORAGE_ITEM.LAYOUT]: {
    maxWidth: 1280,
    fontSize: 14,
    lineHeight: 24,
    grid: {
      columnsNumber: 12,
      columnGutter: 24
    },
    focusLine: 10,
    fontFamily: "'Inter', sans-serif"
  },
  [STORAGE_ITEM.BLOCKS]: [],
  [STORAGE_ITEM.SCENARIOS]: [],
  [STORAGE_ITEM.THESAURUS]: [],
  [STORAGE_ITEM.MONITORING]: []
}

const _getStorageItem = item => {
  const value = STORAGE.getItem(item)

  if (!value || value === '') {
    return null
  }

  return JSON.parse(value)
}

const _setStorageItem = (item, value) => {
  STORAGE.setItem(item, JSON.stringify(value))
}

const _initStorage = () => {
  Object.values(STORAGE_ITEM).forEach(storageItem => {
    const item = _getStorageItem(storageItem)

    if (item) {
      return
    }

    _setStorageItem(storageItem, STORAGE_ITEM_INITIAL_VALUE[storageItem])
  })
}

_initStorage()

/* STORAGE */

export const exportStorage = () => {
  return JSON.stringify(
    Object.values(STORAGE_ITEM).reduce((storage, storageItem) => {
      storage[storageItem] = _getStorageItem(storageItem)
      return storage
    }, {})
  )
}

export const importStorage = storage => {
  Object.entries(JSON.parse(storage)).forEach(([storageItem, value]) => {
    _setStorageItem(storageItem, value)
  })
}

/* LAYOUT */

export const getLayout = () => {
  return _getStorageItem(STORAGE_ITEM.LAYOUT)
}

export const updateLayout = layout => {
  return _setStorageItem(STORAGE_ITEM.LAYOUT, layout)
}

/* BLOCKS */

const _getBlocks = () => {
  return _getStorageItem(STORAGE_ITEM.BLOCKS)
}

export const getScenarioBlocks = scenario => {
  const blocks = _getBlocks()

  return blocks.filter(block => {
    return scenario.blocks.includes(block.id)
  })
}

// const _doesBlockExist = id => {
//   const blocks = _getBlocks()

//   return !!blocks.find(block => {
//     return block.id === id
//   })
// }

const _addBlock = block => {
  const blocks = _getBlocks()

  _setStorageItem(STORAGE_ITEM.BLOCKS, [...blocks, block])
}

const _updateBlock = block => {
  const blocks = _getBlocks()

  _setStorageItem(
    STORAGE_ITEM.BLOCKS,
    blocks.map(_block => {
      if (_block.id !== block.id) {
        return _block
      }

      return block
    })
  )
}

const _deleteBlockById = id => {
  const blocks = _getBlocks()

  _setStorageItem(
    STORAGE_ITEM.BLOCKS,
    blocks.filter(block => {
      return block.id !== id
    })
  )
}

/* SCENARIOS */

export const getScenarios = () => {
  return _getStorageItem(STORAGE_ITEM.SCENARIOS)
}

const _getScenarioById = id => {
  const scenarios = getScenarios()

  return scenarios.find(scenario => {
    return scenario.id === id
  })
}

const _saveScenarios = scenarios => {
  _setStorageItem(STORAGE_ITEM.SCENARIOS, scenarios)

  return scenarios
}

export const addScenario = title => {
  const scenarios = getScenarios()
  const newScenario = {
    id: nanoid(),
    title,
    blocks: []
  }

  return _saveScenarios([...scenarios, newScenario])
}

export const deleteScenarioById = id => {
  const scenarios = getScenarios()
  const scenario = _getScenarioById(id)

  scenario.blocks.forEach(blockId => {
    _deleteBlockById(blockId)
  })

  return _saveScenarios(
    scenarios.filter(scenario => {
      return scenario.id !== id
    })
  )
}

export const updateScenario = scenario => {
  const scenarios = getScenarios()
  const prevScenario = _getScenarioById(scenario.id)

  // delete and update blocks
  prevScenario.blocks.forEach(blockId => {
    const block = scenario.blocks.find(block => {
      return block.id === blockId
    })

    if (!block) {
      _deleteBlockById(blockId)
      return
    }

    _updateBlock(block)
  })

  // add new blocks
  scenario.blocks.forEach(block => {
    const isNewBlock = !prevScenario.blocks.find(blockId => {
      return blockId === block.id
    })

    if (!isNewBlock) {
      return
    }

    _addBlock(block)
  })

  return _saveScenarios(
    scenarios.map(_scenario => {
      if (_scenario.id !== scenario.id) {
        return _scenario
      }

      return {
        ...scenario,
        blocks: scenario.blocks.map(block => {
          return block.id
        })
      }
    })
  )
}

/* THESAURUS */

export const getThesaurus = () => {
  return _getStorageItem(STORAGE_ITEM.THESAURUS)
}

const _saveThesaurus = thesaurus => {
  _setStorageItem(STORAGE_ITEM.THESAURUS, thesaurus)

  return thesaurus
}

export const addTerm = term => {
  const thesaurus = getThesaurus()
  const newTerm = {
    ...term,
    id: nanoid()
  }

  return _saveThesaurus([...thesaurus, newTerm])
}

export const updateTerm = term => {
  const thesaurus = getThesaurus()

  return _saveThesaurus(
    thesaurus.map(_term => {
      if (_term.id !== term.id) {
        return _term
      }

      return term
    })
  )
}

export const deleteTermById = id => {
  const thesaurus = getThesaurus()

  return _saveThesaurus(
    thesaurus.filter(term => {
      return term.id !== id
    })
  )
}

/* MONITORING */

export const getMonitoringRecords = () => {
  return _getStorageItem(STORAGE_ITEM.MONITORING).map(({ id, date }) => {
    return { id, date }
  })
}

export const getMonitoringRecordById = id => {
  const monitoringRecords = _getStorageItem(STORAGE_ITEM.MONITORING)

  return monitoringRecords.find(record => {
    return record.id === id
  })
}

const _saveMonitoringRecords = monitoringRecords => {
  _setStorageItem(STORAGE_ITEM.MONITORING, monitoringRecords)

  return monitoringRecords
}

export const addMonitoringRecord = record => {
  const monitoringRecords = _getStorageItem(STORAGE_ITEM.MONITORING)

  _saveMonitoringRecords([...monitoringRecords, record])
}

export const deleteMonitoringRecordById = id => {
  const monitoringRecords = _getStorageItem(STORAGE_ITEM.MONITORING)

  return _saveMonitoringRecords(
    monitoringRecords.filter(record => {
      return record.id !== id
    })
  )
}

