import { useState, useRef, useEffect } from 'react'
import { useTheme } from '@material-ui/core'

import { circleRectCollision } from '../helpers'

const AUTO_SCROLL_MAX_SPEED = 5
const FOCUS_LINE_INITIAL_VALUE = { cy: 0, height: 0, topY: 0, bottomY: 0 }

const useAutoScroll = props => {
  const { gaze } = props

  const theme = useTheme()

  const [contentContainer, setContentContainer] = useState(null)

  const containerSupportRef = useRef(null)
  const focusLineRef = useRef(FOCUS_LINE_INITIAL_VALUE)

  const getGazeSpaceBounds = () => {
    return contentContainer?.getBoundingClientRect()
  }

  const setFocusLineEffect = () => {
    if (!contentContainer) {
      return
    }

    const getFocusLine = () => {
      const cy =
        (getGazeSpaceBounds().y || 0) + parseFloat(theme.layout.getTop())
      const height = theme.layout.lineHeight

      return {
        cy,
        height,
        topY: cy - height / 2,
        bottomY: cy + height / 2
      }
    }

    const newFocusLine = getFocusLine()

    if (JSON.stringify(newFocusLine) === JSON.stringify(focusLineRef.current)) {
      return
    }

    focusLineRef.current = newFocusLine
  }

  // eslint-disable-next-line react-hooks/exhaustive-deps
  useEffect(setFocusLineEffect, [contentContainer, theme.layout])

  const isGazeInsideGazeSpace = () => {
    const { x, y, width, height } = getGazeSpaceBounds()

    return circleRectCollision(gaze, {
      x,
      y,
      w: width,
      h: height
    })
  }

  const isGazeAboveFocusLine = () => {
    return gaze.y < focusLineRef.current.topY
  }

  const isGazeBelowFocusLine = () => {
    return gaze.y > focusLineRef.current.bottomY
  }

  const getUpLimit = () => {
    return 0
  }

  const getContainerSupportHeight = () => {
    return containerSupportRef.current.getBoundingClientRect().height
  }

  const getBottomLimit = () => {
    return getGazeSpaceBounds().height - getContainerSupportHeight()
  }

  const scroll = () => {
    const getStep = () => {
      const { y, height } = getGazeSpaceBounds()

      if (isGazeAboveFocusLine()) {
        const dy = focusLineRef.current.topY - gaze.y
        const ratio = dy / (focusLineRef.current.topY - y)

        return ratio * AUTO_SCROLL_MAX_SPEED
      }

      if (isGazeBelowFocusLine()) {
        const dy = focusLineRef.current.bottomY - gaze.y
        const ratio = dy / (y + height - focusLineRef.current.bottomY)

        return ratio * AUTO_SCROLL_MAX_SPEED
      }

      return 0
    }

    const step = Math.round(getStep())
    const prevTop = parseFloat(containerSupportRef.current.style.top) || 0

    let top = prevTop + step

    if (top > getUpLimit()) {
      top = getUpLimit()
    } else if (top < getBottomLimit()) {
      top = getBottomLimit()
    }

    containerSupportRef.current.style.top = `${top}px`
  }

  return {
    containerSupportRef,
    setContentContainer,
    isGazeInsideGazeSpace,
    scroll
  }
}

export default useAutoScroll
