import React from 'react'
import { Slider as MuiSlider } from '@material-ui/core'

import { useStyles } from '../LeftDrawer/LeftDrawer.style'

const Slider = props => {
  const { value, step, marks, onChange } = props

  const classes = useStyles()

  const min = marks[0].value
  const max = marks.slice(-1)[0].value

  const _marks = marks.map(({ value, label }, markIndex) => {
    const getLabel = () => {
      const getLabelParts = () => {
        return label.split('_').reduce((label, part, partIndex, parts) => {
          if (partIndex === parts.length - 1) {
            return [...label, part]
          }

          return [...label, part, <br key={partIndex} />]
        }, [])
      }

      const _label = label ? getLabelParts() : value

      if (markIndex === 0) {
        return <span className={classes.sliderMarkLeft}>{_label}</span>
      }

      if (markIndex === marks.length - 1) {
        return <span className={classes.sliderMarkRight}>{_label}</span>
      }

      return <span>{_label}</span>
    }

    return { value, label: getLabel() }
  })

  return (
    <MuiSlider
      className={classes.slider}
      color="primary"
      value={value}
      min={min}
      max={max}
      step={step}
      marks={_marks}
      valueLabelDisplay="auto"
      onChange={onChange}
    />
  )
}

export default Slider
