import React, { useState, useRef, useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Typography } from '@material-ui/core'
import { Alert } from '@material-ui/lab'
import { get as lodashGet, set as lodashSet } from 'lodash'

import { ATOM } from '../../../../constants'

import * as routing from '../../../../routing'

import {
  updateLayout,
  saveLayout
} from '../../../../redux/Layout/layout.actions'

import LeftDrawer from '../../../../ui/LeftDrawer'
import Slider from '../../../../ui/Slider'

import { useStyles } from '../../../../ui/LeftDrawer/LeftDrawer.style'

const layoutParams = [
  {
    param: 'maxWidth',
    label: 'Максимальная ширина области контента',
    min: 1000,
    max: 1920,
    step: 10
  },
  {
    param: 'lineHeight',
    label: 'Высота строки',
    min: ATOM * 3,
    max: ATOM * 4,
    step: 1
  },
  {
    param: 'fontSize',
    label: 'Размер шрифта',
    min: 12,
    max: 16,
    step: 2
  },
  {
    param: 'grid.columnGutter',
    label: 'Ширина межколоночного интервала',
    min: ATOM,
    max: ATOM * ATOM,
    step: ATOM
  },
  {
    param: 'focusLine',
    label: 'Высота линии фокуса',
    min: 0,
    max: 20,
    step: 1
  }
]

const LayoutEditor = () => {
  const dispatch = useDispatch()

  const layout = useSelector(state => state.layout)

  const classes = useStyles()

  const [isLayoutChanged, setIsLayoutChanged] = useState(false)

  const initialLayoutRef = useRef(null)

  const setInitialLayout = () => {
    initialLayoutRef.current = JSON.stringify(layout)
  }

  const setInitialLayoutEffect = () => {
    if (initialLayoutRef.current) {
      return
    }

    setInitialLayout()
  }

  // eslint-disable-next-line react-hooks/exhaustive-deps
  useEffect(setInitialLayoutEffect, [layout])

  const setIsLayoutChangedEffect = () => {
    const _isLayoutChanged = initialLayoutRef.current !== JSON.stringify(layout)

    if (_isLayoutChanged === isLayoutChanged) {
      return
    }

    setIsLayoutChanged(_isLayoutChanged)
  }

  useEffect(setIsLayoutChangedEffect, [isLayoutChanged, layout])

  const resetLayoutEffect = () => {
    return () => {
      dispatch(updateLayout(JSON.parse(initialLayoutRef.current)))
    }
  }

  // eslint-disable-next-line react-hooks/exhaustive-deps
  useEffect(resetLayoutEffect, [])

  const onLayoutParamChange = (param, value) => {
    const prevValue = lodashGet(layout, param)

    if (value === prevValue) {
      return
    }

    const changedLayout = lodashSet(
      JSON.parse(JSON.stringify(layout)),
      param,
      value
    )

    dispatch(updateLayout(changedLayout))
  }

  const onSave = () => {
    dispatch(saveLayout())
    setIsLayoutChanged(false)
    setInitialLayout()
  }

  return (
    <LeftDrawer
      header={{
        title: 'Параметры представления',
        backRoute: routing.ROUTE_ADMIN,
        saveButton: {
          disabled: !isLayoutChanged,
          onSave
        }
      }}
    >
      <div className={classes.drawerItem}>
        <Alert className={classes.info} severity="info">
          Рекомендуется установить такую высоту строки, чтобы она была меньше
          ширины межколоночного интервала.
        </Alert>
        {layoutParams.map(param => {
          const getValue = () => {
            return lodashGet(layout, param.param)
          }

          const getSliderMarks = () => {
            return [param.min, param.max].map(value => {
              return { value }
            })
          }

          return (
            <div key={param.param} className={classes.drawerItemParam}>
              <Typography variant="body1">{param.label}</Typography>
              <Slider
                value={getValue()}
                step={param.step}
                marks={getSliderMarks()}
                onChange={(e, value) => onLayoutParamChange(param.param, value)}
              />
            </div>
          )
        })}
      </div>
    </LeftDrawer>
  )
}

export default LayoutEditor
