import * as types from './thesaurus.types'

const initialState = null

export const thesaurusReducer = (state = initialState, action) => {
  switch (action.type) {
    case types.FETCH_THESAURUS:
    case types.ADD_TERM:
    case types.DELETE_TERM:
    case types.UPDATE_TERM:
      return action.payload
    default:
      return state
  }
}
