import React, { useState, useEffect } from 'react'
import { useParams, Redirect } from 'react-router'
import { useDispatch } from 'react-redux'

import { SCENARIO_MODE } from '../../constants'

import * as routing from '../../routing'

import { getScenarioById } from '../../redux/Scenarios/scenarios.actions'

import ScenarioEditor from './components/ScenarioEditor'
import BlockEditor from './components/BlockEditor'
import Scenario from '../../components/Scenario'

const ScenarioEditorPage = () => {
  const dispatch = useDispatch()

  const { scenarioId, blockId } = useParams()

  const [initialScenario, setInitialScenario] = useState(null)
  const [scenario, setScenario] = useState(null)

  const getScenarioEffect = () => {
    if (scenario) {
      return
    }

    const scenario_ = dispatch(getScenarioById(scenarioId))

    setInitialScenario(scenario_)
    setScenario(scenario_)
  }

  useEffect(getScenarioEffect, [dispatch, scenario, scenarioId])

  if (!scenario) {
    if (scenario === undefined) {
      return <Redirect to={routing.ROUTE_ADMIN} />
    }

    return null
  }

  const isScenarioChanged =
    JSON.stringify(initialScenario) !== JSON.stringify(scenario)

  const setScenarioBlocks = blocks => {
    setScenario({ ...scenario, blocks })
  }

  const setScenarioBlock = block => {
    setScenarioBlocks(
      scenario.blocks.map(_block => {
        if (_block.id !== block.id) {
          return _block
        }

        return block
      })
    )
  }

  const deleteScenarioBlock = block => {
    setScenarioBlocks(
      scenario.blocks.filter(_block => {
        return _block.id !== block.id
      })
    )
  }

  const swapBlocks = (block1, block2) => {
    setScenarioBlocks(
      scenario.blocks.map(block => {
        if (block.id === block1.id) {
          return { ...block, order: block2.order }
        }

        if (block.id === block2.id) {
          return { ...block, order: block1.order }
        }

        return block
      })
    )
  }

  const moveScenarioBlockUp = block => {
    const blockBefore = scenario.blocks
      .sort((b1, b2) => {
        return b2.order - b1.order
      })
      .find(_block => {
        return _block.order < block.order
      })

    if (blockBefore) {
      swapBlocks(block, blockBefore)
    }
  }

  const moveScenarioBlockDown = block => {
    const blockAfter = scenario.blocks
      .sort((b1, b2) => {
        return b1.order - b2.order
      })
      .find(_block => {
        return _block.order > block.order
      })

    if (blockAfter) {
      swapBlocks(block, blockAfter)
    }
  }

  const blockActions = {
    updateParams: setScenarioBlock,
    delete: deleteScenarioBlock,
    moveUp: moveScenarioBlockUp,
    moveDown: moveScenarioBlockDown
  }

  const renderSidebar = () => {
    if (blockId) {
      const block = scenario.blocks.find(block => {
        return block.id === blockId
      })

      if (!block) {
        return <Redirect to={routing.getRouteToScenarioEditor(scenarioId)} />
      }

      return <BlockEditor block={block} blockActions={blockActions} />
    }

    return (
      <ScenarioEditor
        scenario={scenario}
        isScenarioChanged={isScenarioChanged}
        setInitialScenario={setInitialScenario}
        setScenario={setScenario}
      />
    )
  }

  return (
    <>
      {renderSidebar()}
      <Scenario
        scenario={scenario}
        modes={[SCENARIO_MODE.EDIT]}
        blockActions={blockActions}
        setScenarioBlocks={setScenarioBlocks}
      />
    </>
  )
}

export default ScenarioEditorPage
