import React from 'react'
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Button
} from '@material-ui/core'

const AddDialog = props => {
  const { isOpened, title, children, onClose, onAdd } = props

  const _onAdd = () => {
    if (!onAdd()) {
      return
    }

    onClose()
  }

  return (
    <Dialog open={isOpened} fullWidth onClose={onClose}>
      <DialogTitle>{title}</DialogTitle>
      <DialogContent>{children}</DialogContent>
      <DialogActions>
        <Button color="primary" onClick={_onAdd}>
          Добавить
        </Button>
        <Button color="default" onClick={onClose}>
          Отмена
        </Button>
      </DialogActions>
    </Dialog>
  )
}

export default AddDialog
