import React from 'react'
import { Link } from 'react-router-dom'
import {
  Drawer,
  IconButton,
  Button,
  Divider,
  Typography
} from '@material-ui/core'
import { Alert } from '@material-ui/lab'
import { Close, SaveOutlined } from '@material-ui/icons'

// import { UsersList } from '../UsersList/UsersList';

import { useStyles } from './LeftDrawer.style'

const LeftDrawer = props => {
  const { header, children } = props

  const classes = useStyles()

  const renderHeader = () => {
    if (!header) {
      return null
    }

    const renderAdditional = () => {
      if (!header.saveButton) {
        return null
      }

      const renderSaveButton = () => {
        if (!header.saveButton.onSave) {
          return <Alert severity="info">{header.saveButton.message}</Alert>
        }

        return (
          <Button
            variant="outlined"
            color="primary"
            size="small"
            disabled={header.saveButton.disabled}
            // startIcon={<SaveOutlined />}
            onClick={header.saveButton.onSave}
          >
            Сохранить
          </Button>
        )
      }

      return (
        <div className={classes.drawerHeaderAdditional}>
          {renderSaveButton()}
        </div>
      )
    }

    return (
      <>
        <div className={classes.drawerItem}>
          <div className={classes.drawerHeader}>
            <Typography variant="h6">{header.title}</Typography>
            <IconButton size="small" component={Link} to={header.backRoute}>
              <Close />
            </IconButton>
          </div>
          {renderAdditional()}
        </div>
        <Divider />
      </>
    )
  }

  return (
    <Drawer
      className={classes.drawer}
      variant="permanent"
      classes={{
        paper: classes.drawerPaper
      }}
      anchor="left"
    >
      {renderHeader()}
      {children}
      {/* <Switch>
        <Route exact path="/admin/users">
          <UsersList />
        </Route>
      </Switch> */}
    </Drawer>
  )
}

export default LeftDrawer
