import React, { useEffect } from 'react'
import { useHistory } from 'react-router-dom'
import { useDispatch } from 'react-redux'
import { useTheme, Button } from '@material-ui/core'
import { Add } from '@material-ui/icons'
import useAnimationFrame from 'use-animation-frame'
import { nanoid } from 'nanoid'

import {
  SCENARIO_MODE,
  TERM_CLASS_NAME,
  WORD_CLASS_NAME
} from '../../constants'

import { joinClasses, pickKeysFromObject } from '../../helpers'

import * as api from '../../api'
import * as monitoringApi from '../../api/monitoring'

import * as routing from '../../routing'

import useGaze from '../../hooks/useGaze'
import useAutoScroll from '../../hooks/useAutoScroll'

import { toggleAddBlockDialog } from '../../redux/BlockEditor/blockEditor.actions'

import Grid from '../../pages/LayoutEditorPage/components/Grid'
import Content from '../Content'

import { useStyles } from './ContentContainer.style'

import './ContentContainer.css'

const ContentContainer = props => {
  const {
    autoScroll,
    blocks,
    modes,
    cascade,
    containerWidth,
    blockActions,
    expandTerms,
    getTextPartClass
  } = props

  const dispatch = useDispatch()

  const history = useHistory()

  const theme = useTheme()

  const classes = useStyles()

  const { gaze, onGazeMove } = useGaze()

  const {
    containerSupportRef,
    setContentContainer,
    isGazeInsideGazeSpace,
    scroll
  } = useAutoScroll({
    gaze
  })

  useEffect(() => {
    return () => {
      monitoringApi.clearState()
    }
  }, [])

  const doMonitoring = modes.includes(SCENARIO_MODE.VIEW)

  const getTermToExpand = () => {
    const getElementsByClassName = className => {
      return Object.fromEntries(
        [...document.querySelectorAll(`.${className}`)].map(word => {
          const { x, y, width, height } = word.getBoundingClientRect()

          return [word.id, { x, y, w: width, h: height }]
        })
      )
    }

    const contentMarkup = {
      terms: getElementsByClassName(TERM_CLASS_NAME),
      words: getElementsByClassName(WORD_CLASS_NAME)
    }

    const termsToExpand = monitoringApi.getTermsToExpand(gaze, contentMarkup)

    if (!termsToExpand.length) {
      return
    }

    expandTerms(termsToExpand)
  }

  useAnimationFrame(() => {
    if (!doMonitoring) {
      return
    }

    if (!isGazeInsideGazeSpace() || !gaze.isMoving) {
      return
    }

    scroll()

    gaze.isMoving = false

    getTermToExpand()
  })

  const finishViewing = () => {
    const record = {
      id: nanoid(),
      date: Date.now(),
      layout: pickKeysFromObject(
        theme.layout,
        Object.keys(api.STORAGE_ITEM_INITIAL_VALUE[api.STORAGE_ITEM.LAYOUT])
      ),
      containerWidth: containerSupportRef.current.clientWidth,
      blocks,
      monitoringData: monitoringApi.getMonitoringData()
    }

    api.addMonitoringRecord(record)
    history.replace(routing.ROUTE_SCENARIOS)
  }

  // const [readingProgress, setReadingProgress] = useState(null)

  // useEffect(() => {
  //   if (blocks) {
  //     if (!readingProgress) {
  //       const progress = []

  //       blocks.forEach(block => {
  //         if (!block.text) return

  //         block.text.forEach(() => {
  //           progress.push({
  //             completed: false
  //           })
  //         })
  //       })

  //       setReadingProgress(progress)
  //     }
  //   }
  // }, [blocks, readingProgress])

  const renderContent = () => {
    if (!blocks.length) {
      return (
        <Button
          startIcon={<Add />}
          variant="outlined"
          color="primary"
          onClick={() => dispatch(toggleAddBlockDialog())}
        >
          Добавить блок
        </Button>
      )
    }

    const sortBlocks = () => {
      return blocks.sort((b1, b2) => {
        return b1.order - b2.order
      })
    }

    const renderGrid = () => {
      if (!modes.includes(SCENARIO_MODE.EXAMPLE)) {
        return null
      }

      return <Grid />
    }

    return (
      <>
        <div
          ref={containerSupportRef}
          id="contentWidth"
          className={classes.containerSupport}
          style={containerWidth ? { width: `${containerWidth}px` } : {}}
        >
          {renderGrid()}
          <Content
            modes={modes}
            blocks={sortBlocks()}
            autoScroll={autoScroll}
            cascade={cascade}
            blockActions={blockActions}
            finishViewing={finishViewing}
            getTextPartClass={getTextPartClass}
          />
        </div>
        {/* {modes.indexOf('view') !== -1 && readingProgress && (
            <div className="reading-progress-container">
              <div className="reading-progress">
                {readingProgress.map((item, index) => (
                  <div
                    key={index}
                    className={`reading-progress-item ${item.completed ? 'completed' : 'not-completed'}`}
                  />
                ))}
              </div>
            </div>
          )} */}
      </>
    )
  }

  return (
    <div
      ref={setContentContainer}
      id="contentHeight"
      className={joinClasses(
        'content-container',
        autoScroll && 'auto-scroll-content',
        ...modes
      )}
      style={{
        justifyContent: blocks?.length ? 'flex-start' : 'center'
      }}
      onMouseMove={onGazeMove}
    >
      {renderContent()}
    </div>
  )
}

export default ContentContainer
