import * as types from './notification.types'

const initialState = {
  isOpened: false,
  severity: 'success',
  message: null
}

export const notificationReducer = (state = initialState, action) => {
  switch (action.type) {
    case types.SHOW_NOTIFICATION:
      return { ...action.payload, isOpened: true }
    case types.HIDE_NOTIFICATION:
      return initialState
    default:
      return state
  }
}
