import React from 'react'

import { WORD_CLASS_NAME, TERM_CLASS_NAME } from '../../constants'

import { TEXT_PART_TYPE } from '../../api/content'

import { joinClasses } from '../../helpers'

import './SplittedText.css'

const SplittedText = props => {
  const { blockId, paragraphIndex, children, getTextPartClass } = props

  if (typeof children === 'string') {
    return children
  }

  return children.map((part, partIndex) => {
    const isPartTerm = part.type === TEXT_PART_TYPE.TERM
    const isPartWord = part.type === TEXT_PART_TYPE.WORD

    const getClassAndId = () => {
      const id = `${blockId}__${paragraphIndex}__${part.id}__${partIndex}`

      if (isPartTerm) {
        return {
          className: TERM_CLASS_NAME,
          id
        }
      }

      if (isPartWord) {
        return {
          className: WORD_CLASS_NAME,
          id
        }
      }

      return {
        className: undefined,
        id: undefined
      }
    }

    const { className, id } = getClassAndId()

    return (
      <span
        key={partIndex}
        className={joinClasses(
          className,
          getTextPartClass && id && getTextPartClass(id)
        )}
        id={id}
      >
        {part.value}
      </span>
    )
  })
}

export default SplittedText
