import React from 'react'
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Button
} from '@material-ui/core'

const UpdateDialog = props => {
  const { isOpened, title, children, onClose, onUpdate } = props

  const _onUpdate = () => {
    if (!onUpdate()) {
      return
    }

    onClose()
  }

  return (
    <Dialog open={isOpened} fullWidth onClose={onClose}>
      <DialogTitle>{title}</DialogTitle>
      <DialogContent>{children}</DialogContent>
      <DialogActions>
        <Button color="primary" onClick={_onUpdate}>
          Изменить
        </Button>
        <Button color="default" onClick={onClose}>
          Отмена
        </Button>
      </DialogActions>
    </Dialog>
  )
}

export default UpdateDialog
