import React from 'react'
import { Alert } from '@material-ui/lab'

import { useStyles } from './TextAlert.style'

const TextAlert = props => {
  const { severity, children } = props

  const classes = useStyles()

  return (
    <Alert className={classes.alert} severity={severity} variant="standard">
      {children}
    </Alert>
  )
}

export default TextAlert
