import React, { useState } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { Box, Button, TextField, CircularProgress } from '@material-ui/core'
import { Autocomplete } from '@material-ui/lab'
import { Add } from '@material-ui/icons'
import { useDebouncedCallback } from 'use-debounce'

import * as contentApi from '../../../../api/content'

import {
  addTerm,
  updateTerm
} from '../../../../redux/Thesaurus/thesaurus.actions'

import AddDialog from '../../../../ui/AddDialog'
import UpdateDialog from '../../../../ui/UpdateDialog'
import TermCard from '../../../../ui/TermCard'

import { useStyles } from './Terms.style'

const EMPTY_TERM = {
  id: '',
  term: '',
  synonym: '',
  synonyms: [],
  definition: ''
}
const TERM_DIALOG_INITIAL_STATE = {
  dialogType: null,
  errors: {},
  ...EMPTY_TERM
}
const TERM_DIALOG_TYPE = {
  ADD_TERM: 'ADD_TERM',
  UPDATE_TERM: 'UPDATE_TERM'
}
const TERM_SYNONYM_OPTIONS_INITIAL_STATE = []

const Terms = () => {
  const classes = useStyles()

  const dispatch = useDispatch()

  const thesaurus = useSelector(state => state.thesaurus)

  const [termDialogState, setTermDialogState] = useState(
    TERM_DIALOG_INITIAL_STATE
  )
  const [termSynonymOptions, setTermSynonymOptions] = useState(
    TERM_SYNONYM_OPTIONS_INITIAL_STATE
  )

  const getTermSynonymOptions = async term => {
    const synonyms = await contentApi.getTermSynonyms(term)

    setTermSynonymOptions(synonyms)
  }

  const getDebouncedTermSynonymOptions = useDebouncedCallback(
    getTermSynonymOptions,
    1000
  )

  const onTermDialogOpen = (_term = EMPTY_TERM) => {
    const { id, term, synonyms, definition } = _term
    const dialogType = id
      ? TERM_DIALOG_TYPE.UPDATE_TERM
      : TERM_DIALOG_TYPE.ADD_TERM

    if (dialogType === TERM_DIALOG_TYPE.ADD_TERM) {
      setTermSynonymOptions(TERM_SYNONYM_OPTIONS_INITIAL_STATE)
    } else if (dialogType === TERM_DIALOG_TYPE.UPDATE_TERM) {
      getDebouncedTermSynonymOptions(term)
    }

    setTermDialogState({
      dialogType,
      id,
      term,
      synonym: '',
      synonyms,
      definition,
      errors: {}
    })
  }

  const onTermDialogClose = () => {
    setTermDialogState(TERM_DIALOG_INITIAL_STATE)
  }

  const onTermDialogTermChange = e => {
    const term = e.target.value

    if (term) {
      getDebouncedTermSynonymOptions(term)
    } else {
      setTermSynonymOptions(TERM_SYNONYM_OPTIONS_INITIAL_STATE)
      getDebouncedTermSynonymOptions.cancel()
    }

    setTermDialogState({ ...termDialogState, term, errors: {} })
  }

  const onTermDialogDefinitionChange = e => {
    setTermDialogState({
      ...termDialogState,
      definition: e.target.value,
      errors: {}
    })
  }

  const onTermDialogSynonymsChange = (e, synonyms) => {
    setTermDialogState({ ...termDialogState, synonyms, errors: {} })
  }

  const onTermDialogSynonymChange = (e, synonym) => {
    setTermDialogState({ ...termDialogState, synonym, errors: {} })
  }

  const onAddTerm = () => {
    const result = dispatch(
      addTerm({
        term: termDialogState.term,
        synonyms: termDialogState.synonyms,
        definition: termDialogState.definition
      })
    )

    if (result?.errors) {
      setTermDialogState({ ...termDialogState, errors: result.errors })
      return false
    }

    return true
  }

  const onUpdateTerm = () => {
    const result = dispatch(
      updateTerm({
        id: termDialogState.id,
        term: termDialogState.term,
        synonyms: termDialogState.synonyms,
        definition: termDialogState.definition
      })
    )

    if (result?.errors) {
      setTermDialogState({
        ...termDialogState,
        errors: result.errors
      })
      return false
    }

    return true
  }

  const areTermSynonymOptionsloading =
    getDebouncedTermSynonymOptions.isPending()

  const _getTermSynonymOptions = () => {
    if (!termDialogState.synonym) {
      return termSynonymOptions
    }

    const doesSynonymExist = termSynonymOptions.find(synonym => {
      return synonym === termDialogState.synonym
    })

    if (doesSynonymExist) {
      return termSynonymOptions
    }

    return [termDialogState.synonym, ...termSynonymOptions]
  }

  const renderTermDialogFields = () => {
    return (
      <>
        <TextField
          autoFocus
          required
          fullWidth
          margin="normal"
          variant="outlined"
          error={!!termDialogState.errors.term}
          label="Термин"
          helperText={
            termDialogState.errors.term ||
            'В начальной форме: ед.ч., м.р., И.п., инфинитив'
          }
          value={termDialogState.term}
          onChange={e => {
            onTermDialogTermChange(e)
          }}
        />
        <Autocomplete
          multiple
          value={termDialogState.synonyms}
          onChange={onTermDialogSynonymsChange}
          inputValue={termDialogState.synonym}
          onInputChange={onTermDialogSynonymChange}
          options={_getTermSynonymOptions()}
          getOptionLabel={option => option}
          filterSelectedOptions
          renderInput={params => (
            <TextField
              {...params}
              fullWidth
              margin="normal"
              variant="outlined"
              label="Синонимы"
              InputProps={{
                ...params.InputProps,
                endAdornment: (
                  <>
                    {areTermSynonymOptionsloading ? (
                      <CircularProgress color="primary" size={20} />
                    ) : null}
                    {params.InputProps.endAdornment}
                  </>
                )
              }}
            />
          )}
        />
        <TextField
          required
          multiline
          fullWidth
          margin="normal"
          variant="outlined"
          error={!!termDialogState.errors.definition}
          label="Определение"
          value={termDialogState.definition}
          helperText={termDialogState.errors.definition}
          onChange={e => {
            onTermDialogDefinitionChange(e)
          }}
        />
      </>
    )
  }

  return (
    <>
      <AddDialog
        isOpened={termDialogState.dialogType === TERM_DIALOG_TYPE.ADD_TERM}
        title="Добавление термина"
        onAdd={onAddTerm}
        onClose={onTermDialogClose}
      >
        {renderTermDialogFields()}
      </AddDialog>
      <UpdateDialog
        isOpened={termDialogState.dialogType === TERM_DIALOG_TYPE.UPDATE_TERM}
        title="Редактирование термина"
        onUpdate={onUpdateTerm}
        onClose={onTermDialogClose}
      >
        {renderTermDialogFields()}
      </UpdateDialog>
      <Box className={classes.container}>
        <Box className={classes.header}>
          <Button
            size="small"
            variant="outlined"
            color="primary"
            startIcon={<Add />}
            onClick={() => {
              onTermDialogOpen()
            }}
          >
            Добавить термин
          </Button>
        </Box>
        <Box className={classes.cards}>
          {thesaurus.map(term => {
            return (
              <TermCard
                key={term.id}
                term={term}
                showMenu
                onTermDialogOpen={onTermDialogOpen}
              />
            )
          })}
        </Box>
      </Box>
    </>
  )
}

export default Terms
