import React from 'react'
import { useTheme } from '@material-ui/core'

import { FORMAT_TAG, BLOCK_TYPE } from '../../constants'

import SplittedText from '../../ui/SplittedText'
import TextAlert from '../../ui/TextAlert'
import TermCard from '../../ui/TermCard'

import { useStyles as useBlockStyles } from '../Block/Block.style'
import { useStyles } from './Text.style'

import './Text.css'

const Text = props => {
  const { block, parentType, cascade, getTextPartClass } = props

  const theme = useTheme()

  const blockClasses = useBlockStyles()
  const classes = useStyles()

  const getFlex = () => {
    if (!cascade && parentType === BLOCK_TYPE.TEXT_IMAGE_VERTICAL) {
      return 1
    }

    return 'none'
  }

  const renderTitle = () => {
    if (!block.title) {
      return null
    }

    return <div className={classes.title}>{block.title}</div>
  }

  return (
    <div
      className={blockClasses.item}
      style={{
        width: block.params.textWidth
          ? theme.layout.getWidth(block.params.textWidth)
          : 'auto',
        flex: getFlex(),
        position: 'sticky',
        alignSelf: 'flex-start',
        top: theme.layout.getTop()
      }}
    >
      {renderTitle()}
      <div className={classes.text}>
        {block.text.map((p, pIndex) => {
          const renderText = text => {
            return (
              <SplittedText
                blockId={block.id}
                paragraphIndex={pIndex}
                getTextPartClass={getTextPartClass}
              >
                {text}
              </SplittedText>
            )
          }

          if (p.id) {
            return (
              <TermCard
                key={pIndex}
                term={p}
                splittedTextData={{
                  blockId: block.id,
                  paragraphIndex: pIndex
                }}
              />
            )
          }

          const formatTag = p[0]?.value

          if (Object.values(FORMAT_TAG).includes(formatTag)) {
            const severity = Object.entries(FORMAT_TAG)
              .find(([, tag]) => {
                return tag === formatTag
              })[0]
              .toLowerCase()

            return (
              <TextAlert key={pIndex} severity={severity}>
                {renderText(p.slice(1))}
              </TextAlert>
            )
          }

          return <p key={pIndex}>{renderText(p)}</p>
        })}
      </div>
    </div>
  )
}

export default Text
