import { combineReducers } from 'redux'

import { layoutReducer as layout } from './Layout/layout.reducer'
import { scenariosReducer as scenarios } from './Scenarios/scenarios.reducer'
import { notificationReducer as notification } from './Notification/notification.reducer'
import { thesaurusReducer as thesaurus } from './Thesaurus/thesaurus.reducer'
import { blockEditorReducer as blockEditor } from './BlockEditor/blockEditor.reducer'

export const rootReducer = combineReducers({
  layout,
  scenarios,
  notification,
  thesaurus,
  blockEditor
})
