import React from 'react'
import { useSelector } from 'react-redux'
import { useTheme } from '@material-ui/core'

import { GOLDEN_RATIO, SCENARIO_MODE, BLOCK_TYPE } from '../../constants'

import * as contentApi from '../../api/content'

import LayoutEditor from './components/LayoutEditor'
import Scenario from '../../components/Scenario'

const LayoutEditorPage = () => {
  const thesaurus = useSelector(state => state.thesaurus)

  const theme = useTheme()

  const exampleScenario = {
    blocks: [
      {
        type: BLOCK_TYPE.TEXT_IMAGE_HORIZONTAL,
        title: 'Заголовок информационного блока',
        text: [
          'Компьютеры коренным образом изменили жизнь каждого человека. Несколько десятилетий назад люди даже не знали ничего об этих электронных устройствах, тогда как в наши дни даже маленький ребенок может управлять этой машиной. Почти все современные технологии зависит от компьютеров, которые используются для хранения данных: файлов, секретной информации, банковских операций и прочего.',
          'Сегодня люди не представляют свою жизнь без современных технологий, в частности, без компьютера.',
          'Но проблема в том, что вместе с положительным эффектом, компьютеры оказывают негативное влияние на человека.'
        ].map(p => {
          return contentApi.splitTextIntoParts(p, thesaurus, false)
        }),
        image: {
          src: './assets/block-image-1.jpg',
          height: 0,
          size: 'cover'
        },
        params: {
          width: theme.layout.grid.columnsNumber,
          textWidth: Math.floor(GOLDEN_RATIO * theme.layout.grid.columnsNumber)
        }
      }
    ]
  }

  return (
    <>
      <LayoutEditor />
      <Scenario scenario={exampleScenario} modes={[SCENARIO_MODE.EXAMPLE]} />
    </>
  )
}

export default LayoutEditorPage
