import React from 'react'
import { useHistory } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import { nanoid } from 'nanoid'
import {
  useTheme,
  Dialog,
  DialogTitle,
  List,
  ListItem,
  ListItemAvatar,
  Avatar,
  ListItemText
} from '@material-ui/core'
import {
  Notes,
  Image,
  VerticalSplit,
  HorizontalSplit
} from '@material-ui/icons'

import { GOLDEN_RATIO, BLOCK_TYPE } from '../../../../constants'

import * as routing from '../../../../routing'

import { toggleAddBlockDialog } from '../../../../redux/BlockEditor/blockEditor.actions'

import { useStyles } from './AddBlockDialog.style'

const blocks = [
  {
    type: BLOCK_TYPE.TEXT,
    name: 'Текст',
    icon: <Notes />
  },
  {
    type: BLOCK_TYPE.IMAGE,
    name: 'Изображение',
    icon: <Image />
  },
  {
    type: BLOCK_TYPE.TEXT_IMAGE_HORIZONTAL,
    name: 'Текст и изображение по горизонтали',
    icon: <VerticalSplit />
  },
  {
    type: BLOCK_TYPE.TEXT_IMAGE_VERTICAL,
    name: 'Текст и изображение по вертикали',
    icon: <HorizontalSplit style={{ transform: 'rotate(180deg)' }} />
  }
]

const getRandomImageSrc = () => {
  const links = [
    './assets/block-image-1.jpg',
    './assets/block-image-2.jpg',
    './assets/block-image-3.jpg'
  ]

  return links[Math.floor(Math.random() * links.length)]
}

const AddBlockDialog = props => {
  const { scenario, setScenarioBlocks } = props

  const theme = useTheme()

  const history = useHistory()
  const classes = useStyles()
  const dispatch = useDispatch()
  const blockEditorState = useSelector(state => state.blockEditor)
  const { addBlockDialog } = blockEditorState

  const onClose = () => {
    dispatch(toggleAddBlockDialog())
  }

  const onAddBlock = type => {
    const block = {
      id: nanoid(),
      order: addBlockDialog.order || 0,
      type,
      params: {}
    }

    if (type.includes(BLOCK_TYPE.IMAGE)) {
      block.image = {
        src: getRandomImageSrc(),
        size: 'cover'
      }

      if (type === BLOCK_TYPE.IMAGE) {
        block.params.width = theme.layout.grid.columnsNumber
      }

      if (type === BLOCK_TYPE.TEXT_IMAGE_HORIZONTAL) {
        block.image.height = 0
      } else {
        block.image.height = 8
      }
    }

    if (type.includes(BLOCK_TYPE.TEXT)) {
      block.title = 'Заголовок информационного блока'
      block.text = ['Основной текст информационного блока']

      if (type === BLOCK_TYPE.TEXT_IMAGE_HORIZONTAL) {
        block.params.width = theme.layout.grid.columnsNumber
        block.params.textWidth = Math.floor(GOLDEN_RATIO * block.params.width)
      } else if (type === BLOCK_TYPE.TEXT_IMAGE_VERTICAL) {
        block.params.width = Math.floor(theme.layout.grid.columnsNumber / 2)
      } else {
        block.params.textWidth = theme.layout.grid.columnsNumber
      }
    }

    // change order of other blocks

    const getReorderedBlocks = () => {
      if (!addBlockDialog.direction) {
        return scenario.blocks
      }

      return scenario.blocks.map(_block => {
        if (addBlockDialog.direction > 0 && _block.order <= block.order) {
          return { ..._block, order: _block.order - 1 }
        }

        if (addBlockDialog.direction < 0 && _block.order >= block.order) {
          return { ..._block, order: _block.order + 1 }
        }

        return _block
      })
    }

    const reorderedBlocks = getReorderedBlocks()

    setScenarioBlocks([...reorderedBlocks, block])
    onClose()

    history.push(routing.getRouteToBlockEditor(scenario.id, block.id))
  }

  return (
    <Dialog open={addBlockDialog.show} onClose={onClose}>
      <DialogTitle>Добавление блока</DialogTitle>
      <List>
        {blocks.map(block => (
          <ListItem
            key={block.type}
            button
            onClick={() => onAddBlock(block.type)}
          >
            <ListItemAvatar>
              <Avatar className={classes.avatar}>{block.icon}</Avatar>
            </ListItemAvatar>
            <ListItemText primary={block.name} />
          </ListItem>
        ))}
      </List>
    </Dialog>
  )
}

export default AddBlockDialog
