import { makeStyles } from '@material-ui/core/styles'

export const useStyles = makeStyles(theme => ({
  drawer: {
    width: '400px',
    zIndex: 9
  },
  drawerPaper: {
    position: 'static'
  },
  drawerHeader: {
    display: 'flex',
    alignItems: 'center',
    '& > *:first-child': {
      width: '100%',
      marginRight: theme.spacing(1),
      fontWeight: 'normal'
    },
    '& > *:last-child': {
      marginRight: theme.spacing(-1)
    }
  },
  drawerHeaderAdditional: {
    marginTop: theme.spacing(1)
  },
  drawerItem: {
    padding: theme.spacing(2, 4)
  },
  drawerItemTitle: {
    fontWeight: 'normal',
    marginBottom: theme.spacing(2)
  },
  drawerItemParam: {
    marginBottom: theme.spacing(2),
    '&:last-child': {
      marginBottom: theme.spacing(1)
    }
  },
  slider: {
    '& .MuiSlider-markLabel': {
      display: 'flex',
      width: 0,
      justifyContent: 'center',
      transform: 'none'
    }
  },
  sliderMarkLeft: {
    width: 0
  },
  sliderMarkRight: {
    transform: 'translateX(-47%)'
  },
  drawerItemParamSwitch: {
    display: 'flex',
    alignItems: 'center',
    '& > *:first-child': {
      width: '100%'
    },
    '& > *:last-child': {
      marginRight: theme.spacing(-1)
    }
  },
  listItemWithButton: {
    display: 'flex',
    alignItems: 'center',
    '& > p': {
      width: '100%'
    }
  },
  nestedListItem: {
    padding: theme.spacing(1, 4, 1, 6)
  },
  info: {
    marginBottom: theme.spacing(2)
  },
  listItem: {
    padding: theme.spacing(1, 4)
  }
}))
