import * as types from './scenarios.types'

const initialState = null

export const scenariosReducer = (state = initialState, action) => {
  switch (action.type) {
    case types.FETCH_SCENARIOS:
    case types.ADD_SCENARIO:
    case types.DELETE_SCENARIO:
    case types.UPDATE_SCENARIO:
      return action.payload
    default:
      return state
  }
}
