import * as api from '../../api'

import * as types from './layout.types'

import { showNotification } from '../Notification/notification.actions'

export const fetchLayout =
  (force = false) =>
  (dispatch, getState) => {
    const { layout } = getState()

    if (layout && !force) {
      return
    }

    dispatch({ type: types.FETCH_LAYOUT, payload: api.getLayout() })
  }

export const updateLayout = _layout => (dispatch, getState) => {
  const { layout } = getState()

  if (JSON.stringify(layout) === JSON.stringify(_layout)) {
    return
  }

  dispatch({ type: types.UPDATE_LAYOUT, payload: _layout })
}

export const saveLayout = () => (dispatch, getState) => {
  const { layout } = getState()

  api.updateLayout(layout)
  dispatch(showNotification('success', 'Параметры представления сохранены'))
}

export const temporarySetLayout = layout => dispatch => {
  dispatch({ type: types.TEMPORARY_SET_LAYOUT, payload: layout })
}

export const resetLayout = () => dispatch => {
  dispatch(fetchLayout(true))
}
