import React from 'react'

import Image from '../Image'
import Text from '../Text'

import './TextImageHorizontal.css'

const TextImageHorizontal = props => {
  const { block } = props

  const text = <Text {...props} />
  const image = <Image {...props} />
  const items = block.image.position === 'left' ? [image, text] : [text, image]

  return items.map((item, index) => ({ ...item, key: index }))
}

export default TextImageHorizontal
