import React, { useState, useEffect } from 'react'
import { useParams, Redirect } from 'react-router-dom'
import { useDispatch } from 'react-redux'
import { Box, CircularProgress } from '@material-ui/core'
import html2canvas from 'html2canvas'

import * as api from '../../api'

import * as routing from '../../routing'

import {
  temporarySetLayout,
  resetLayout
} from '../../redux/Layout/layout.actions'

import useMonitoringCanvas from '../../hooks/useMonitoringCanvas'

import MonitoringRecordSidebar from './components/MonitoringRecordSidebar'
import Scenario from '../../components/Scenario'

import { useStyles } from './MonitoringRecordPage.style'

const MonitoringRecordPage = () => {
  const dispatch = useDispatch()

  const { monitoringRecordId } = useParams()

  const classes = useStyles()

  const { canvasRef, isZoomed, drawImage, onToggleCanvasZoom } =
    useMonitoringCanvas()

  const [monitoringRecord, setMonitoringRecord] = useState(null)
  const [isContentCaptured, setIsContentCaptured] = useState(null)

  const fetchMonitoringRecordEffect = () => {
    if (monitoringRecord) {
      return
    }

    const _monitoringRecord = api.getMonitoringRecordById(monitoringRecordId)

    if (_monitoringRecord) {
      dispatch(temporarySetLayout(_monitoringRecord.layout))
    }

    setMonitoringRecord(_monitoringRecord)
  }

  useEffect(fetchMonitoringRecordEffect, [
    dispatch,
    monitoringRecord,
    monitoringRecordId
  ])

  const resetLayoutEffect = () => {
    return () => {
      dispatch(resetLayout())
    }
  }

  // eslint-disable-next-line react-hooks/exhaustive-deps
  useEffect(resetLayoutEffect, [])

  const captureContentEffect = () => {
    if (!monitoringRecord || isContentCaptured) {
      return
    }

    //! В будущем заменить setTimout на флаг, определяющий полную прогрузку контента

    setTimeout(() => {
      html2canvas(document.querySelector('#contentWidth'), {
        backgroundColor: null
      }).then(canvas => {
        drawImage(canvas)
        setIsContentCaptured(true)
      })
    }, 1000)
  }

  useEffect(captureContentEffect, [
    drawImage,
    isContentCaptured,
    monitoringRecord
  ])

  if (!monitoringRecord) {
    if (monitoringRecord === undefined) {
      return <Redirect to={routing.ROUTE_ADMIN} />
    }

    return null
  }

  const getTextPartClass = id => {
    const part = monitoringRecord.monitoringData[id]

    if (!part) {
      return 'unread'
    }

    if (part.isRecognized) {
      return 'recognized'
    }

    return 'unrecognized'
  }

  return (
    <>
      <MonitoringRecordSidebar />
      <Box
        className={classes.canvasContainer}
        style={
          isContentCaptured
            ? {}
            : {
                display: 'flex',
                flexDirection: 'column',
                justifyContent: 'center',
                alignItems: 'center'
              }
        }
      >
        {!isContentCaptured && <CircularProgress />}
        <canvas
          ref={canvasRef}
          width={0}
          height={0}
          style={{
            maxWidth: isZoomed ? 'none' : '100%',
            cursor: isZoomed ? 'zoom-out' : 'zoom-in'
          }}
          onClick={onToggleCanvasZoom}
        />
      </Box>
      <div style={{ width: 0 }}>
        {!isContentCaptured && (
          <Scenario
            scenario={{
              blocks: monitoringRecord.blocks
            }}
            modes={['monitoring-record']}
            cascade
            containerWidth={monitoringRecord.containerWidth}
            getTextPartClass={getTextPartClass}
          />
        )}
      </div>
    </>
  )
}

export default MonitoringRecordPage
