import * as types from './blockEditor.types'

export const toggleAddBlockDialog =
  (order = null, direction = null) =>
  (dispatch, getState) => {
    const { blockEditor } = getState()

    dispatch({
      type: types.TOGGLE_ADD_BLOCK_DIALOG,
      payload: { show: !blockEditor.addBlockDialog.show, order, direction }
    })
  }
