import React from 'react'
import { useParams } from 'react-router-dom'
import {
  useTheme,
  TextField,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Typography,
  Divider
} from '@material-ui/core'
import { Alert, AlertTitle } from '@material-ui/lab'
import { get as lodashGet, set as lodashSet } from 'lodash'

import {
  GOLDEN_RATIO_SMALL,
  GOLDEN_RATIO,
  FORMAT_TAG,
  BLOCK_TYPE
} from '../../../../constants'

import * as routing from '../../../../routing'

import LeftDrawer from '../../../../ui/LeftDrawer'
import Slider from '../../../../ui/Slider'

import { useStyles } from '../../../../ui/LeftDrawer/LeftDrawer.style'

const BlockEditor = props => {
  const { block, blockActions } = props

  const theme = useTheme()

  const classes = useStyles()

  const { scenarioId } = useParams()

  const onBlockParamChange = (param, value) => {
    const prevValue = lodashGet(block, param)

    if (value === prevValue) {
      return
    }

    const changedBlock = lodashSet(
      JSON.parse(JSON.stringify(block)),
      param,
      value
    )

    blockActions.updateParams(changedBlock)
  }

  return (
    <LeftDrawer
      header={{
        title: 'Редактирование блока',
        backRoute: routing.getRouteToScenarioEditor(scenarioId),
        saveButton: {
          message:
            'Сохранение блоков происходит после сохранения самого сценария.'
        }
      }}
    >
      <div className={classes.drawerItem}>
        <Typography className={classes.drawerItemTitle} variant="h6">
          Представление блока
        </Typography>
        <div className={classes.drawerItemParam}>
          <Typography variant="body1">Ширина блока</Typography>
          <Slider
            value={block.params.width || block.params.textWidth}
            step={1}
            marks={[
              {
                value: 4
              },
              {
                value: theme.layout.grid.columnsNumber
              }
            ]}
            onChange={(e, value) =>
              onBlockParamChange(
                block.params.width ? 'params.width' : 'params.textWidth',
                value
              )
            }
          />
        </div>
        {block.type === BLOCK_TYPE.TEXT_IMAGE_HORIZONTAL && (
          <div className={classes.drawerItemParam}>
            <Typography variant="body1">
              Отношение ширины текста и изображения
            </Typography>
            <Slider
              value={block.params.textWidth}
              step={1}
              marks={[
                {
                  value: 0,
                  label: 'Текст'
                },
                {
                  value: Math.floor(GOLDEN_RATIO_SMALL * block.params.width),
                  label: 'Золотое_сечение'
                },
                {
                  value: Math.floor(GOLDEN_RATIO * block.params.width),
                  label: 'Золотое_сечение'
                },
                {
                  value: block.params.width,
                  label: 'Изображение'
                }
              ]}
              onChange={(e, value) =>
                onBlockParamChange('params.textWidth', value)
              }
            />
          </div>
        )}
      </div>
      <Divider />
      {block.type.includes(BLOCK_TYPE.TEXT) && (
        <>
          <div className={classes.drawerItem}>
            <Typography className={classes.drawerItemTitle} variant="h6">
              Текстовое наполнение
            </Typography>
            <Alert className={classes.info} severity="info">
              <AlertTitle>Использование тегов</AlertTitle>
              Для форматирования основного текста доступны следующие теги:
              <div style={{ marginTop: '8px' }}>
                <Typography variant="body2">
                  <Typography
                    component="span"
                    variant="subtitle2"
                    // style={{ color: '#2196f3' }}
                  >
                    {FORMAT_TAG.INFO}
                  </Typography>
                  &nbsp;&mdash; информация
                </Typography>
                <Typography variant="body2">
                  <Typography
                    component="span"
                    variant="subtitle2"
                    // style={{ color: '#ff9800' }}
                  >
                    {FORMAT_TAG.WARNING}
                  </Typography>
                  &nbsp;&mdash; предупреждение
                </Typography>
                <Typography variant="body2">
                  <Typography
                    component="span"
                    variant="subtitle2"
                    // style={{ color: '#f44336' }}
                  >
                    {FORMAT_TAG.ERROR}
                  </Typography>
                  &nbsp;&mdash; ошибка
                </Typography>
              </div>
            </Alert>
            <div className={classes.drawerItemParam}>
              <TextField
                label="Заголовок"
                variant="outlined"
                fullWidth
                value={block.title}
                onChange={e => onBlockParamChange('title', e.target.value)}
              />
            </div>
            <div className={classes.drawerItemParam}>
              <TextField
                label="Основной текст"
                multiline
                variant="outlined"
                fullWidth
                value={block.text.join('\n')}
                onChange={e =>
                  onBlockParamChange('text', e.target.value.split('\n'))
                }
              />
            </div>
          </div>
          {block.type.includes(`${BLOCK_TYPE.TEXT}-${BLOCK_TYPE.IMAGE}`) && (
            <Divider />
          )}
        </>
      )}
      {block.type.includes(BLOCK_TYPE.IMAGE) && (
        <div className={classes.drawerItem}>
          <Typography className={classes.drawerItemTitle} variant="h6">
            Изображение
          </Typography>
          <div className={classes.drawerItemParam}>
            <TextField
              label="Ссылка"
              variant="outlined"
              fullWidth
              value={block.image.src}
              onChange={e => onBlockParamChange('image.src', e.target.value)}
            />
          </div>
          <div className={classes.drawerItemParam}>
            <FormControl variant="outlined" fullWidth>
              <InputLabel id="block-image-size">Масштаб</InputLabel>
              <Select
                labelId="block-image-size"
                label="Масштаб"
                value={block.image.size || 'cover'}
                onChange={e => onBlockParamChange('image.size', e.target.value)}
              >
                <MenuItem value="cover">Заполнение</MenuItem>
                <MenuItem value="contain">Целиком</MenuItem>
              </Select>
            </FormControl>
          </div>
          {block.type === BLOCK_TYPE.TEXT_IMAGE_HORIZONTAL && (
            <div className={classes.drawerItemParam}>
              <FormControl variant="outlined" fullWidth>
                <InputLabel id="block-image-position">
                  Позиционирование
                </InputLabel>
                <Select
                  labelId="block-image-position"
                  label="Позиционирование"
                  value={block.image.position || 'right'}
                  onChange={e =>
                    onBlockParamChange('image.position', e.target.value)
                  }
                >
                  <MenuItem value="right">Справа</MenuItem>
                  <MenuItem value="left">Слева</MenuItem>
                </Select>
              </FormControl>
            </div>
          )}
          <div className={classes.drawerItemParam}>
            <Typography variant="body1">Высота изображения</Typography>
            <Slider
              value={block.image.height || 0}
              step={1}
              marks={[
                {
                  value: 0,
                  label: 'Авто'
                },
                {
                  value: 16
                }
              ]}
              onChange={(e, value) => onBlockParamChange('image.height', value)}
            />
          </div>
        </div>
      )}
    </LeftDrawer>
  )
}

export default BlockEditor
